import produce from 'immer';
import AUTH_ACTIONS_REQUEST from './types';

const INITIAL_STATE = {
  isAuthenticated: false,
  isLoading: false,
  isRememberAccess: false,
  username: '',
  password: '',
  token: '',
};

export default function auth(state = INITIAL_STATE, action) {
  switch (action.type) {
    case AUTH_ACTIONS_REQUEST.REQUEST:
      return produce(state, (draft) => {
        draft.isLoading = true;
      });

    case AUTH_ACTIONS_REQUEST.LOGIN_SUCCESS:
      return produce(state, (draft) => {
        draft.isAuthenticated = true;
        draft.isLoading = false;
        draft.token = action?.payload?.data.token;
        if (action?.payload?.data.isRememberAccess) {
          draft.isRememberAccess = action?.payload?.data.isRememberAccess;
          draft.username = action?.payload?.data.username;
          draft.password = action?.payload?.data.password;
        }
      });

    case AUTH_ACTIONS_REQUEST.LOGOUT:
      return produce(state, (draft) => {
        draft.isAuthenticated = false;
        draft.isLoading = false;
        draft.token = '';
      });

    case AUTH_ACTIONS_REQUEST.REQUEST_ERROR:
      return produce(state, (draft) => {
        draft.isLoading = false;
      });

    case AUTH_ACTIONS_REQUEST.DOES_NOT_REMEMBER_ACCESS:
      return produce(state, (draft) => {
        draft.isRememberAccess = false;
        draft.username = '';
        draft.password = '';
      });

    default:
      return state;
  }
}
