import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: 24,
    minHeight: 100,
    display: 'flex',
    flexDirection: 'column',
    gap: 14,
  },
  linkImage: {
    textTransform: 'none',
    color: theme.appColors.switch,
    '&:hover': {
      textDecoration: 'underline',
      backgroundColor: 'transparent'
    },
  },
  head: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  title: {
    fontWeight: 500,
    fontSize: 20,
  },
  container: {
    width: 414,
    height: 230,
  },
  image: {
    width: '100%',
    height: '100%',
    borderRadius: 15,
  },
  text: {
    textAlign: 'center',
  }
}));
