import { useEffect, useState } from 'react';
import lodash from 'lodash';
import { toast } from 'react-hot-toast';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import { useLog } from '../../../hooks/useLog';

export const useNotification = () => {
  const [notifications, setNotifications] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showNotifications = () => {
    api
      .get('/users/push-notification')
      .then((response) => {
        setIsLoading(false);
        const orderData = lodash.orderBy(response.data.data, ['created_at'], ['desc']);
        setNotifications(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const onSubmitNotification = (newNotification) => {
    api
      .post('/users/push-notification/all', newNotification)
      .then((response) => {
        sendLog(`A notificação “${newNotification.title}” foi criada.`)
        toast.success('A notificação foi enviada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        toast.error('Ocorreu um erro no envio da notificação. Tente novamente mais tarde. ');
      });
  };

  const deleteNotification = (id, title) => {
    api
      .delete(`/users/push-notification/${id}`)
      .then((response) => {
        sendLog(`A notificação “${title}” foi excluída.`)
        toast.success('A notificação foi removida com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        toast.error('Ocorreu um erro na remoção da notificação. Tente novamente mais tarde. ');
      });
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Data_Hora: item.dateCampanha,
      Titulo: item.title,
      Descricao: item.description.props.text,
      // url: item.link_url.props.text,
    }));

    downloadXlsx(data, 'Notificações cadastradas.xlsx');
  };

  useEffect(() => {
    showNotifications();
  }, []);

  return {
    notifications,
    isLoading,
    onSubmitNotification,
    deleteNotification,
    downloadExcel,
  };
};
