import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import lodash from 'lodash';
import moment from 'moment';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';
import { IdSapNotFoundError, IdSapErrorDefault } from '../components/ToastError';
import formatCPForCNPJ from '../../../utils/helpers/formatCPForCNPJ';

export const useInclusao = () => {
  const [users, setUsers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showUsers = () => {
    api.get('/users/inclusion-requests').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((user) => ({
          id: user.id,
          identification: formatCPForCNPJ(user.identification),
          createdAt: moment(user.user_creation_date).format('DD/MM/YYYY'),
          name: user.name,
          phone: formatPhoneNumber(user.telephone),
          email: user.email,
        }));
        const orderData = lodash.orderBy(data, ['createdAt'], ['desc']);
        setUsers(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const checkIdSap = (identification, setIdSap) => {
    api
      .get(`/users/check-upgrade-sap?identification=${identification.replace(/\D+/g, '')}`)
      .then((response) => {
        const idSap = response.data.data;
        setIdSap(idSap && idSap.sap_code);
      })
      .catch((error) => {
        const err = error.response.data.errors[0].message;
        if (err === 'User not found on SAP') {
          toast(() => <IdSapNotFoundError />, {
            style: {
              maxWidth: '600px',
            },
            duration: Infinity,
          });
        } else {
          toast(() => <IdSapErrorDefault />, {
            style: {
              maxWidth: '600px',
            },
            duration: Infinity,
          });
        }
      });
  };

  const changeUser = (id, newUser) => {
    api
      .patch(`/users/inclusion-requests/${id}`, newUser)
      .then(() => {
        sendLog(`Cliente “${newUser.name}” foi alterado.`);
        toast.success(`Cliente “${newUser.name}” alterado com sucesso!`);
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => console.log('=====error====', error));
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Cadastrado_Em: item.createdAt,
      Nome: item.name,
      Telefone_SAP: item.oldPhone,
      Telefone_alterado: item.newPhone,
      Email_SAP: item.oldEmail,
      Email_alterado: item.newEmail,
    }));
    downloadXlsx(data, 'Revisão de cadastro dos usuários.xlsx');
  };

  useEffect(() => {
    showUsers();
  }, []);

  return {
    users,
    isLoading,
    checkIdSap,
    changeUser,
    downloadExcel,
  };
};
