import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    padding: '16px 16px 24px',
    position: 'absolute',
    minWidth: 350,
  },
  title: {
    fontWeight: '700',
    fontSize: 20,
    lineHeight: '160%',
    textAlign: 'center',
    letterSpacing: '0.15px',
    marginBottom: theme.spacing(1),
  },
  input: {
    margin: theme.spacing(1, 0, 1, 0),
    maxWidth: '330px',
    width: 'inherit',
    '&:label': {
      textTransform: 'capitalize',
    },
  },
  eye: {
    marginRight: 10,
    height: 15,
    width: 15,
  },

  btn: {
    position: 'static',
    width: 'inherit',
    height: 36,
  },
}));
