import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: 24,
    minHeight: 100,
    display: 'flex',
    flexDirection: 'column',
  },
  btnAddCampanha: {
    margin: theme.spacing(2),
    width: theme.spacing(12),
    fontSize: 14,
    color: theme.appColors.primaryColor,
    borderColor: theme.appColors.primaryColor,
  },
  imgAddCircle: {
    marginRight: theme.spacing(1),
  },
  head: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  divider: {
    width: 'inherit',
    margin: theme.spacing(1, 0, 1, 0),
  },
  msgError: ({ display }) => ({
    display: display,
    width: '100%',
    height: 48,
    backgroundColor: '#E73434',
    borderRadius: 4,
    color: 'white',
    alignItems: 'center',
    justifyContent: 'center',
    transition: 'display 0.5s',
  }),
  titlePage: {
    fontWeight: 500,
    fontSize: 20,
  },
  container: {
    display: 'grid',
    gridTemplateColumns: '1fr 2fr',
    marginTop: theme.spacing(1),
    gap: 24
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: 20,
  },
  title: {
    width: '100%',
  },
  dateContainer: {
    display: 'flex',
    gap: 10,
  },
  dateForm: {
    width: '50%',
    padding: 3
  },
  description: {
    width: '100%',
  },
  url: {
    width: '100%',
  },
  urlInput: {
    fontSize: 14
  },
  setContainer: {
    display: 'flex',
    justifyContent: 'space-between',
  },
  btnForm: {
    display: 'flex',
    justifyContent: 'flex-end',
    gap: 15,
  },
  btnCancel: {
    display: 'flex',
    gap: 20,
  },
  btnSend: {
    color: theme.appColors.pink,
    borderColor: theme.appColors.pink,
  },
}));
