import lodash from 'lodash';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { useLog } from '../../../hooks/useLog';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';

export const useEvents = () => {
  const [eventos, setEventos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [ufOptions, setUfOptions] = useState([]);

  const { sendLog } = useLog();

  const showEvents = () => {
    api.get('/events').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((evento) => ({
          dateOrder: `${evento.date} - ${evento.hour}`,
          id: evento.id,
          day: `${moment(evento.date).format('DD/MM/YYYY')} - ${evento.hour}`,
          evento: evento.name,
          uf: evento.uf,
          city: evento.city === undefined ? '-' : evento.city,
        }));
        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setEventos(orderData);
      },
      (error) => {
        setIsLoading(false);

        switch (error.response.data.errors[0].message) {
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const handleEvents = (id, setEvents) => {
    api.get(`/events/${id}`).then(
      (response) => {
        const setRadioGroupUf = response.data.data.uf === 'BR' ? 'br' : 'regional';
        const setUf = response.data.data.uf === 'BR' ? '' : response.data.data.uf;
        setEvents.setName(response.data.data.name);
        setEvents.setDate(moment(response.data.data.date));
        setEvents.setHour(`${response.data.data.date} ${response.data.data.hour}`);
        setEvents.setRadioGroup(setRadioGroupUf);
        setEvents.setUf(setUf);
        setEvents.setCity(response.data.data.city);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Inform a valid id.':
            toast.error('Informe um evento valido.');
            break;
          case 'No itens found.':
            toast.error('Nenhum item encontrado.');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao buscar o evento.');
        }
      },
    );
  };

  const showStates = () => {
    api.get('/states').then(
      (response) => {
        const data = response.data.data.map((state) => ({
          label: state.uf,
        }));
        const orderedUf = lodash.orderBy(data, ['label'], ['asc']);
        setUfOptions(orderedUf);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao listar os Estados.');
        }
      },
    );
  };

  const handleStats = (value, setCityOptions) => {
    api.get(`states/cities?uf=${value.label}`).then(
      (response) => {
        const data = response.data.data.map((cityO) => ({
          label: cityO.name,
        }));
        const orderedCities = lodash.orderBy(data, ['label'], ['asc']);
        setCityOptions(orderedCities);
      },
      (error) => {
        if (error.response && error.response.status === 401) {
          toast.error(error.response.data.errors[0].message);
        } else {
          toast.error(error.response.data.errors[0].message);
        }
      },
    );
  };

  const changeEvents = (id, newEvents, handleClose) => {
    api.put(`/events/${id}`, newEvents).then(
      (response) => {
        sendLog(`O evento "${newEvents.name}" foi alterado`);
        setIsLoading(false);
        handleClose();
        toast.success('Evento Alterado com sucesso.');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'Inform a valid id':
            toast.error('Informe um evento valido.');
            break;
          case 'Date cannot be empty':
            toast.error('A data não pode estar vazia');
            break;
          case 'Date format is incorrect.':
            toast.error('O formato da data está incorreto.');
            break;
          case 'Hour cannot be empty.':
            toast.error('A hora não pode estar vazia.');
            break;
          case 'Hour format is incorrect.':
            toast.error('O formato da hora está incorreto.');
            break;
          case 'Name cannot be empty':
            toast.error('O nome do evento não pode estar vazio.');
            break;
          case 'UF cannot be empty.':
            toast.error('UF não pode estar vazio.');
            break;
          case 'City cannot be empty.':
            toast.error('A cidade não pode estar vazia.');
            break;
          case 'No itens found.':
            toast.error('Nenhum item encontrado.');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao editar o evento.');
        }
      },
    );
  };

  const onSubmitEvents = (newEvents) => {
    api.post('/events', newEvents).then(
      () => {
        sendLog(`O evento "${newEvents.name}" foi cadastrado`);
        setIsLoading(false);
        toast.success('Evento cadastrado com Sucesso.');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'Date cannot be empty':
            toast.error('A data não pode estar vazia');
            break;
          case 'Date format is incorrect.':
            toast.error('O formato da data está incorreto.');
            break;
          case 'Hour cannot be empty.':
            toast.error('A hora não pode estar vazia.');
            break;
          case 'Hour format is incorrect.':
            toast.error('O formato da hora está incorreto.');
            break;
          case 'Name cannot be empty':
            toast.error('O nome do evento não pode estar vazio.');
            break;
          case 'UF cannot be empty.':
            toast.error('UF não pode estar vazio.');
            break;
          case 'City cannot be empty.':
            toast.error('A cidade não pode estar vazia.');
            break;
          case 'No itens found.':
            toast.error('Nenhum item encontrado.');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao cadastrar o evento.');
        }
      },
    );
  };

  const deleteEvents = (id, evento, handleClose) => {
    api.delete(`/events/${id}`).then(
      (response) => {
        sendLog(`O evento "${evento}" foi excluído`);
        toast.success('Evento removido com sucesso.');
        handleClose();
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Inform a valid id':
            toast.error('Informe um evento valido.');
            break;
          case 'No itens found.':
            toast.error('Nenhum item encontrado.');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao remover o evento.');
        }
      },
    );
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Data_E_Hora: item.day,
      Evento: item.evento,
      UF: item.uf,
      Cidade: item.city,
    }));
    downloadXlsx(data, 'Eventos.xlsx');
  };

  useEffect(() => {
    showEvents();
    showStates();
  }, []);

  return {
    eventos,
    handleEvents,
    ufOptions,
    changeEvents,
    handleStats,
    onSubmitEvents,
    isLoading,
    deleteEvents,
    downloadExcel,
  };
};
