import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: theme.spacing(2),
    marginBottom: theme.spacing(2),
  },
  head: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  titlePage: {
    fontWeight: 500,
    fontSize: 20,
  },
  divider: {
    width: 'inherit',
    margin: theme.spacing(1, 0, 0, 0),
  },
  form: {
    display: 'flex',
    flexDirection: 'column',
    gap: 20,
    width: 827,
    marginTop: theme.spacing(3),
  },
  container: {
    gap: 10,
  },
  date: {
    maxWidth: 150,
    color: theme.appColors.Gray,
    borderColor: theme.appColors.Gray,
  },
  timer: {
    maxWidth: 120,
    color: theme.appColors.Gray,
    borderColor: theme.appColors.Gray,
  },
  input: { width: 'calc(100% - 290px)' },
  radio: {
    paddingLeft: '14px',
  },
  radioLabel: {
    fontSize: 13,
  },
  uf: { maxWidth: 80 },
  cidade: { width: 'calc(100% - 333px)' },
  btnForm: {
    display: 'flex',
    justifyContent: 'center',
    gap: 15,
    marginTop: theme.spacing(3),
    marginBottom: theme.spacing(3),
  },
  btnCancel: {
    display: 'flex',
    gap: 20,
  },
  btnSend: {
    color: theme.appColors.pink,
    borderColor: theme.appColors.pink,
  },
}));
