import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import lodash from 'lodash';
import moment from 'moment';
import images from '../../../assets/imgs/index';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';
import MessageModal from '../components/MessageModal';

const statusFormat = {
  undefined: '',
  Novo: (
    <>
      <img src={images.GreenDot} alt="logo" alignItems="center" style={{ marginRight: 5 }} />
      Novo
    </>
  ),
  'Em aberto': (
    <>
      <img src={images.YellowDot} alt="logo" alignItems="center" style={{ marginRight: 5 }} />
      Em aberto
    </>
  ),
  Finalizado: (
    <>
      <img src={images.RedDot} alt="logo" alignItems="center" style={{ marginRight: 5 }} />
      Finalizado
    </>
  ),
};

export const useChamados = () => {
  const [chamados, setChamados] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showChamados = () => {
    api.get('/users/tickets').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map(
          ({
            id,
            created_at: createdAt,
            user_name: name,
            user_phone_number: phone,
            user_email: email,
            message,
            status,
          }) => ({
            id,
            createdAt: moment(createdAt).format('DD/MM/YYYY - HH:mm'),
            name,
            phone: formatPhoneNumber(phone),
            email,
            message: <MessageModal msg={message} />,
            status: statusFormat[status],
          }),
        );
        const orderData = lodash.orderBy(data, ['createdAt'], ['desc']);
        setChamados(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const changeInfoUser = (newRegitration, name) => {
    api
      .put('/registrations/requests', newRegitration)
      .then(() => {
        sendLog(`Cadastro do cliente “${name}” foi alterado.`);
        toast.success('O cadastro foi alterado com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch(() => {
        toast.error('Ocorreu um erro na revisão do cadastro. Tente novamente mais tarde. ');
      });
  };

  const deleteUserReview = (id, title) => {
    api
      .delete(`/registrations/requests/${id}`)
      .then(() => {
        sendLog(`Revisão do cliente “${title}” foi retirada.`);
        toast.success('A revisão foi retirada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => console.log('=====error====', error));
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Cadastrado_Em: item.createdAt,
      Solicitante: item.name,
      Telefone: item.phone,
      Email: item.email,
      Mensagem: item.message,
      Status: item.status,
    }));
    downloadXlsx(data, 'Lista de Chamados.xlsx');
  };

  useEffect(() => {
    showChamados();
  }, []);

  return {
    chamados,
    isLoading,
    changeInfoUser,
    deleteUserReview,
    downloadExcel,
  };
};
