/* eslint-disable import/order */
import { useState } from 'react';
import * as M from '@material-ui/core';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { columns } from './Columns';
import { useStyles } from './styles';
import { useVendas } from '../../hooks/useVendas';
import LensIcon from '@material-ui/icons/Lens';

export default function MesaDeGraoOrdenAutomaticaTable() {
  const classes = useStyles();
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();

  const [status, setStatus] = useState('');
  const { notifications } = useVendas(status === 'Todos' ? '' : status);

  const { searchText, auxRows, requestSearch } = useRequestSearch(notifications);

  const plural = auxRows.length >= 2 ? 'ordens cadastradas ' : 'ordem cadastrada';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural
  };



  return (
    <>
      {notifications && notifications.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <FormControl sx={{ m: 0.5, minWidth: 155, mr: 2 }} variant="standard" fullwidth size="small">
              <InputLabel id="demo-simple-select-label">Status</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={status}
                label="Todos"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="Todos">Todos</MenuItem>
                <MenuItem value="Executada">Executada</MenuItem>
                <MenuItem value="Pendente">Pendente</MenuItem>
                <MenuItem value="Cancelado">Cancelado</MenuItem>
              </Select>
            </FormControl>
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table" className={classes.table}>
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} style={column.style} align="center" className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows &&
                  auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    return (
                      <M.TableRow hover role="checkbox" tabIndex={0} key={row.code}>
                        {columns.map((column) => {
                          const value = row[column.id];
                          return column.label === 'CPF/CNPJ' ? (
                            <M.TableCell
                              key={column.id}
                              align={column.align}
                              style={{ color: '#DB3F7A', textDecoration: 'underline', cursor: 'pointer' }}
                            >
                              {value}
                            </M.TableCell>
                          )  : column.label === 'Status' ? (
                            <M.TableCell
                              key={column.id}
                              align="center"
                              style={{display: 'flex', padding:'47px 10px'}}
                            >
                              <LensIcon style={{color: `${value === 'Pendente' ? '#FFC107' : value === 'Cancelado' ? '#E73434' : '#4CAF50'}`, fontSize: '10px', display: 'flex', margin: '6 6 0'}}/>
                              {value}
                            </M.TableCell>
                          ) : (
                            <M.TableCell key={column.id} align="center">
                            {column.format && typeof value === 'number' ? column.format(value) : value}
                          </M.TableCell>
                          )
                        })}

                      </M.TableRow>

                    )
                  })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <M.Paper className={classes.root}>
          <TableHeader  tableHeaderProps={tableHeaderProps}>
            <FormControl style={{ color: '#FFF'}} sx={{ m: 0.5, minWidth: 155, mr: 2 }} variant="standard" fullwidth size="small">
              <InputLabel id="demo-simple-select-label">Status</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={status}
                label="Todos"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="Todos">Todos</MenuItem>
                <MenuItem value="Executada">Executada</MenuItem>
                <MenuItem value="Pendente">Pendente</MenuItem>
                <MenuItem value="Cancelado">Cancelado</MenuItem>
              </Select>
            </FormControl>
          </TableHeader>
              <M.Grid style={{marginTop: '20%'}} container direction="row" justifyContent="center" alignItems="center">
                <img src={images.ordensAutomaticasVazia} alt="logo" alignItems="center" className={classes.img} />
              </M.Grid>
              <M.Grid style={{ display: 'flex', justifyContent: 'center', marginTop: '20px', marginBottom: '20%'}}>
                <M.Typography className={classes.textCard}>Não foi encontrado nenhum resultado no status selecionado.</M.Typography>
              </M.Grid>
          </M.Paper>
        </>
      )}
    </>
  );
}
