import * as M from '@material-ui/core';
import toast from 'react-hot-toast';
import { useStyles } from './styles';

export const IdSapNotFoundError = () => {
  const classes = useStyles();

  return (
    <M.Grid className={classes.root}>
      <M.Grid className={classes.head}>
        <M.Typography className={classes.title}>Operação não disponível (ID SAP não encontrado)</M.Typography>
      </M.Grid>
      <M.Grid className={classes.container}>
        <M.Typography className={classes.text}>
          Por gentileza, verifique se o mesmo está corretamente cadastrado e tente novamente.
        </M.Typography>
      </M.Grid>
      <M.Grid className={classes.btnForm}>
        <M.Button onClick={() => toast.remove()}>OK</M.Button>
      </M.Grid>
    </M.Grid>
  );
};

export const IdSapErrorDefault = () => {
  const classes = useStyles();

  return (
    <M.Grid className={classes.root}>
      <M.Grid className={classes.head}>
        <M.Typography className={classes.title}>Operação não disponível (serviço em manutenção)</M.Typography>
      </M.Grid>
      <M.Grid className={classes.container}>
        <M.Typography className={classes.text}>
          Não foi possível fazer a consulta do cadastro do usuário na base SAP. Por gentileza, retorne novamente mais
          tarde.
        </M.Typography>
      </M.Grid>
      <M.Grid className={classes.btnForm}>
        <M.Button onClick={() => toast.remove()}>OK</M.Button>
      </M.Grid>
    </M.Grid>
  );
};
