/* eslint-disable prefer-const */
/* eslint-disable import/no-unresolved */
import { Grid } from '@material-ui/core';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import Typography from '@material-ui/core/Typography';
import OutlinedFlagOutlinedIcon from '@material-ui/icons/OutlinedFlagOutlined';
import AttachMoneyOutlinedIcon from '@material-ui/icons/AttachMoneyOutlined';
import GavelIcon from '@material-ui/icons/Gavel';
import AssessmentOutlinedIcon from '@material-ui/icons/AssessmentOutlined';
import BugReportOutlinedIcon from '@material-ui/icons/BugReportOutlined';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import DashboardIcon from '@material-ui/icons/Dashboard';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import EventNoteIcon from '@material-ui/icons/EventNote';
import WorkOutlineOutlinedIcon from '@material-ui/icons/WorkOutlineOutlined';
import StorageOutlinedIcon from '@material-ui/icons/StorageOutlined';
import PinDropOutlinedIcon from '@material-ui/icons/PinDropOutlined';
import ExpandMoreOutlinedIcon from '@material-ui/icons/ExpandMoreOutlined';
import CompareArrowsOutlinedIcon from '@material-ui/icons/CompareArrowsOutlined';
import RecordVoiceOverOutlined from '@material-ui/icons/RecordVoiceOverOutlined';
import NotificationsNoneOutlinedIcon from '@material-ui/icons/NotificationsNoneOutlined';
import ContactSupportOutlinedIcon from '@material-ui/icons/ContactSupportOutlined';
import GroupIcon from '@material-ui/icons/Group';
import HowToVoteIcon from '@material-ui/icons/HowToVote';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import PersonIcon from '@material-ui/icons/Person';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import React from 'react';
import { useHistory } from 'react-router-dom';
import { useStyles } from './styles';

export default function Sidebar() {
  const classes = useStyles();
  const history = useHistory();

  let selectedDashboard = false;
  let selectedAccess = false;
  let selectedUser = false;
  let selectedInclusaoSap = false;
  let selectedRevisaoCadastro = false;
  let selectedLimit = false;
  let selectedEvent = false;
  let selectedAnticipation = false;
  let selectedReagendamento = false;
  let selectedCampanhas = false;
  let selectedNotification = false;
  let selectedMesaDeGraos = false;
  let selectedMesaDeGraosLocais = false;
  let selectedMesaDeGraosOrdensAutomaticas = false;
  let selectedChamados = false;
  let selectedLog = false;
  let selectedLia = false;

  const getLastItem = (thePath) => thePath.substring(thePath.lastIndexOf('/') + 1);
  const page = getLastItem(document.URL);
  switch (page) {
    case 'acessos':
      selectedAccess = true;
      break;
    case 'revisao-de-limite':
      selectedLimit = true;
      break;
    case 'eventos':
      selectedEvent = true;
      break;
    case 'antecipacao':
      selectedAnticipation = true;
      break;
    case 'reagendamento':
      selectedReagendamento = true;
      break;
    case 'campanhas':
      selectedCampanhas = true;
      break;
    case 'notificacoes':
      selectedNotification = true;
      break;
    case 'chamados':
      selectedChamados = true;
      break;
    case 'logs':
      selectedLog = true;
      break;
    case 'lia':
      selectedLia = true;
      break;
    case 'revisao-de-cadastro':
      selectedRevisaoCadastro = true;
      break;
    case 'inclusao-sap':
      selectedInclusaoSap = true;
      break;
    case 'dashboard':
      selectedDashboard = true;
      break;
    case 'mesa-de-graos':
      selectedMesaDeGraos = true;
      break;
    case 'mesa-de-graos-locais':
      selectedMesaDeGraosLocais = true;
      break;
    case 'mesa-de-graos-ordens-automaticas':
      selectedMesaDeGraosOrdensAutomaticas = true;
      break;
    default:
      selectedUser = true;
  }

  return (
    <>
      <Grid>
        <Typography className={classes.text}>Home</Typography>
        <List>
          <ListItem
            button
            className={selectedDashboard ? classes.bg : classes.none}
            onClick={() => history.push('/dashboard')}
          >
            <ListItemIcon>
              <DashboardIcon className={selectedDashboard ? classes.img : classes.none} />
            </ListItemIcon>
            <ListItemText>
              <Typography>Dashboard</Typography>
            </ListItemText>
          </ListItem>
        </List>
      </Grid>
      <Grid>
        <Typography className={classes.text}>Backoffice</Typography>
        <List>
          <ListItem
            button
            className={selectedAccess ? classes.bg : classes.none}
            onClick={() => history.push('/acessos')}
          >
            <ListItemIcon>
              <LockOutlinedIcon className={selectedAccess ? classes.img : classes.none} />
            </ListItemIcon>
            <ListItemText>
              <Typography>Acessos</Typography>
            </ListItemText>
          </ListItem>
        </List>
      </Grid>
      <Grid>
        <Typography className={classes.text}>Minha Lavoro App</Typography>
        <Accordion className={classes.accordion}>
          <AccordionSummary
            expandIcon={<ExpandMoreOutlinedIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            className={classes.accordion}
          >
            <ListItemIcon>
              <GroupIcon />
            </ListItemIcon>
            <ListItemText className='margin: 0'>
              <Typography>Usuários</Typography>
            </ListItemText>
          </AccordionSummary>
          <AccordionDetails className={classes.accordionDetails}>
            <List className={classes.itens}>
              <ListItem
                button
                className={selectedUser ? classes.bg : classes.none}
                onClick={() => history.push('/usuarios')}
              >
                <ListItemIcon>
                  <PersonIcon className={selectedUser ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Cad. de usuários</Typography>
                </ListItemText>
              </ListItem>
              {/* <ListItem
                button
                className={selectedInclusaoSap ? classes.bg : classes.none}
                onClick={() => history.push('/inclusao-sap')}
              >
                <ListItemIcon>
                  <PersonAddIcon className={selectedInclusaoSap ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Sol. inclusão SAP</Typography>
                </ListItemText>
              </ListItem> */}
              <ListItem
                button
                className={selectedRevisaoCadastro ? classes.bg : classes.none}
                onClick={() => history.push('/revisao-de-cadastro')}
              >
                <ListItemIcon>
                  <CompareArrowsOutlinedIcon className={selectedRevisaoCadastro ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Sol. de revisões</Typography>
                </ListItemText>
              </ListItem>
            </List>
          </AccordionDetails>
        </Accordion>
        <Accordion className={classes.accordion}>
          <AccordionSummary
            expandIcon={<ExpandMoreOutlinedIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            className={classes.accordion}
          >
            <ListItemIcon>
              <AttachMoneyOutlinedIcon />
            </ListItemIcon>
            <ListItemText>
              <Typography>Comercial</Typography>
            </ListItemText>
          </AccordionSummary>
          <AccordionDetails className={classes.accordionDetails}>
            <List className={classes.itens}>
              <ListItem
                button
                className={selectedLimit ? classes.bg : classes.none}
                onClick={() => history.push('/revisao-de-limite')}
              >
                <ListItemIcon>
                  <HowToVoteIcon className={selectedLimit ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Rev. de limites</Typography>
                </ListItemText>
              </ListItem>
              <ListItem
                button
                className={selectedAnticipation ? classes.bg : classes.none}
                onClick={() => history.push('/antecipacao')}
              >
                <ListItemIcon>
                  <LocalAtmIcon className={selectedAnticipation ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Antecipação Pgto</Typography>
                </ListItemText>
              </ListItem>
              <ListItem
                button
                className={selectedReagendamento ? classes.bg : classes.none}
                onClick={() => history.push('/reagendamento')}
              >
                <ListItemIcon>
                  <EventNoteIcon className={selectedReagendamento ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Reagendamentos</Typography>
                </ListItemText>
              </ListItem>
            </List>
          </AccordionDetails>
        </Accordion>
        <Accordion className={classes.accordion}>
          <AccordionSummary
            expandIcon={<ExpandMoreOutlinedIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            className={classes.accordion}
          >
            <ListItemIcon>
              <OutlinedFlagOutlinedIcon />
            </ListItemIcon>
            <ListItemText>
              <Typography>Marketing</Typography>
            </ListItemText>
          </AccordionSummary>
          <AccordionDetails className={classes.accordionDetails}>
            <List className={classes.itens}>
              <ListItem
                button
                className={selectedCampanhas ? classes.bg : classes.none}
                onClick={() => history.push('/campanhas')}
              >
                <ListItemIcon>
                  <RecordVoiceOverOutlined className={selectedCampanhas ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Campanhas</Typography>
                </ListItemText>
              </ListItem>
              <ListItem
                button
                className={selectedEvent ? classes.bg : classes.none}
                onClick={() => history.push('/eventos')}
              >
                <ListItemIcon>
                  <CalendarTodayIcon className={selectedEvent ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Eventos</Typography>
                </ListItemText>
              </ListItem>
              <ListItem
                button
                className={selectedNotification ? classes.bg : classes.none}
                onClick={() => history.push('/notificacoes')}
              >
                <ListItemIcon>
                  <NotificationsNoneOutlinedIcon className={selectedNotification ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Notificações</Typography>
                </ListItemText>
              </ListItem>
            </List>
          </AccordionDetails>
        </Accordion>
        <Accordion className={classes.accordion}>
          <AccordionSummary
            expandIcon={<ExpandMoreOutlinedIcon />}
            aria-controls="panel1a-content"
            id="panel1a-header"
            className={classes.accordion}
          >
            <ListItemIcon>
              <WorkOutlineOutlinedIcon />
            </ListItemIcon>
            <ListItemText>
              <Typography>Mesa de Grãos</Typography>
            </ListItemText>
          </AccordionSummary>
          <AccordionDetails className={classes.accordionDetails}>
            <List className={classes.itens}>
              <ListItem
                button
                className={selectedMesaDeGraos ? classes.bg : classes.none}
                onClick={() => history.push('/mesa-de-graos')}
              >
                <ListItemIcon>
                  <StorageOutlinedIcon className={selectedMesaDeGraos ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Solicitações</Typography>
                </ListItemText>
              </ListItem>
              {/* <ListItem
                button
                className={selectedMesaDeGraosLocais ? classes.bg : classes.none}
                onClick={() => history.push('/mesa-de-graos-locais')}
              >
                <ListItemIcon>
                  <PinDropOutlinedIcon className={selectedMesaDeGraosLocais ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>locais</Typography>
                </ListItemText>
              </ListItem> */}
              <ListItem
                button
                className={selectedMesaDeGraosOrdensAutomaticas ? classes.bg : classes.none}
                onClick={() => history.push('/mesa-de-graos-ordens-automaticas')}
              >
                <ListItemIcon>
                  <GavelIcon className={selectedMesaDeGraosOrdensAutomaticas ? classes.img : classes.none} />
                </ListItemIcon>
                <ListItemText>
                  <Typography>Ordens Automáticas</Typography>
                </ListItemText>
              </ListItem>
            </List>
          </AccordionDetails>
        </Accordion>
        <List>
          <ListItem button className={selectedLog ? classes.bg : classes.none} onClick={() => history.push('/logs')}>
            <ListItemIcon>
              <AssessmentOutlinedIcon className={selectedLog ? classes.img : classes.none} />
            </ListItemIcon>
            <ListItemText>
              <Typography>Log de Alterações</Typography>
            </ListItemText>
          </ListItem>
          <ListItem
            button
            className={selectedChamados ? classes.bg : classes.none}
            onClick={() => history.push('/chamados')}
          >
            <ListItemIcon>
              <ContactSupportOutlinedIcon className={selectedChamados ? classes.img : classes.none} />
            </ListItemIcon>
            <ListItemText>
              <Typography>Chamados</Typography>
            </ListItemText>
          </ListItem>
        </List>
      </Grid>
      <Grid>
        <Typography className={classes.text}>LIA</Typography>
        <List>
          <ListItem button className={selectedLia ? classes.bg : classes.none} onClick={() => history.push('/lia')}>
            <ListItemIcon>
              <BugReportOutlinedIcon className={selectedLia ? classes.img : classes.none} />
            </ListItemIcon>
            <ListItemText>
              <Typography>Base doenças/pragas</Typography>
            </ListItemText>
          </ListItem>
        </List>
      </Grid>
    </>
  );
}
