import * as M from '@material-ui/core';
import RecordVoiceOverOutlined from '@material-ui/icons/RecordVoiceOverOutlined';
import moment from 'moment';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import TooltipUi from '../../components/Tooltip';
import CampanhasTable from './components/CampanhasTable/index';
import ShowImage from './components/ShowImage/index';
import ShowInCarousel from './components/ShowInCarousel';
import { TEXT_MODULE } from './constants/Text';
import { useCampanha } from './hooks/useCampanha';
import { useStyles } from './styles';

function Campanhas() {
  const { campanhas, isLoading } = useCampanha();

  const classes = useStyles();

  const formatCampanhas = campanhas && campanhas.map((campanha) => {
    const publicoAlvo = campanha.is_just_for_clients === true ? 'Clientes Lavoro' : 'Todos'
    let newStatus
    if (campanha.show_in_home === "false" ) {
      newStatus = false
    } else {
      newStatus = campanha.show_in_home
    }

    return {
      id: campanha.id,
      orderData: moment(campanha.created_at).format('DD/MM/YYYY - HH:mm'),
      title: campanha.title,
      image: <ShowImage text='Ver imagem' image={campanha.image_url} />,
      status: <ShowInCarousel id={campanha.id} checked={newStatus} campanhaTitle={campanha.title} />,
      description: <TooltipUi text={campanha.description} limitCharacters={55} />,
      dateCampanha: `${moment(campanha.start_date).format('DD/MM/YYYY')} até ${moment(campanha.end_date).format('DD/MM/YYYY')}`,
      link_url: <TooltipUi text={campanha.link_url} />,
      public: publicoAlvo,
      position: campanha.position
    }
  })

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid className={classes.containerTitle}>
            <RecordVoiceOverOutlined className={classes.img} />
            <M.Typography className={classes.titlePage}>{TEXT_MODULE.TITLE}</M.Typography>
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <CampanhasTable campanhas={formatCampanhas} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Campanhas);
