import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import CloseIcon from '@material-ui/icons/Close';
import DeleteOutlineIcon from '@material-ui/icons/DeleteOutline';
import { useState } from 'react';
import { connect } from 'react-redux';
import { usePestTable } from '../../../hooks/usePestTable';
import { useStyles } from './styles';

function RemoveProduto({ name, keyWord }) {
  const { deletePest } = usePestTable();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleDelete = () => {
    const data = { name, keyword: keyWord };
    deletePest(data, handleClose);
  };

  return (
    <>
      <M.Button className={classes.btn} onClick={handleOpen}>
        <DeleteOutlineIcon className={classes.iconBtn} />
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper elevation={3} className={classes.root}>
          <M.Grid
            container
            direction="column"
            justifyContent="space-evenly"
            alignItems="flex-start"
            className={classes.container}
          >
            <M.Grid item className={classes.textContainer}>
              <M.Grid item container direction="row" justifyContent="space-between" alignItems="center">
                <M.Typography className={classes.title}>Remover Praga/Doença</M.Typography>
                <M.IconButton className={classes.close} onClick={handleClose}>
                  <ClearIcon />
                </M.IconButton>
              </M.Grid>
              <M.Divider className={classes.divider} />
              <M.Typography className={classes.text}>
                Você confirma a remoção da praga/doença <span style={{ fontWeight: 'bold' }}>{name}</span>?
              </M.Typography>
            </M.Grid>
            <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                <CloseIcon className={classes.icon} />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={handleDelete}>
                <CheckIcon className={classes.icon} />
                CONFIRMAR
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(RemoveProduto);
