import { useEffect, useState } from 'react';
import lodash from 'lodash';
import { toast } from 'react-hot-toast';
import moment from 'moment';
import TooltipUi from '../../../components/Tooltip';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';

const formCurrency = new Intl.NumberFormat('pt-BR', {
  style: 'currency',
  currency: 'BRL',
  minimumFractionDigits: 2,
});

export const useVendas = (filteredStatus) => {
  const [notifications, setNotifications] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const endpointFilteredOptions = filteredStatus
    ? `grain-table/sell-request?status=${filteredStatus}`
    : 'grain-table/sell-request';

  const showVendas = () => {
    api
      .get(endpointFilteredOptions)
      .then((response) => {
        setIsLoading(false);
        const data = response.data.data.map(
          ({
            created_at: createdAt,
            identification,
            name,
            culture_name: cultureName,
            delivery_place: deliveryPlace,
            price,
            quantity,
            total_price: totalPrice,
            status,
            id,
            email,
            sap_telephone: sapTelephone,
          }) => {
            return {
              orderDate: moment(createdAt).format('DD/MM/YYYY - HH:mm'),
              id,
              identification: (
                <TooltipUi
                  text={
                    identification.length === 11
                      ? identification.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, '$1.$2.$3-$4')
                      : identification.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, '$1.$2.$3/$4-$5')
                  }
                  limitCharacters={15}
                  type
                  data={{ email, sapTelephone, name }}
                />
              ),
              name,
              culture_name: cultureName,
              delivery_place: deliveryPlace,
              price: formCurrency.format(price),
              quantity,
              total_price: formCurrency.format(totalPrice),
              status,
            };
          },
        );
        const orderData = lodash.orderBy(data, ['created_at'], ['desc']);
        setNotifications(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.response.data.errors[0].message === 'Request failed with status code 422') {
          console.log('');
        }
        if (error.response.data.errors[0].message === 'No itens found.') {
          setNotifications([])
          toast.error(`Não há resultados para o filtro "${filteredStatus}".`)
        }
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const changeStatusVenda = (id, orderNumero, status) => {
    api
      .patch(`/grain-table/sell-request/${id}`, {
        status,
      })
      .then((response) => {
        sendLog(`o Status da venda de numero “${orderNumero}” alterada p/ ${status}.`);
        toast.success('Status Alterado com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        toast.error('Ocorreu um erro, Tente novamente mais tarde.');
      });
  };

  useEffect(() => {
    showVendas();
  }, [filteredStatus]);

  return {
    notifications,
    isLoading,
    changeStatusVenda,
  };
};
