import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import toast from 'react-hot-toast';
import { connect } from 'react-redux';
import { useNotification } from '../../hooks/useNotification';
import { useStyles } from './styles';

function ConfirmNotification({ data, closeModal, group }) {
  const { onSubmitNotification } = useNotification();
  const classes = useStyles();

  const confirmButton = () => {
    toast.remove();
    closeModal();
    onSubmitNotification(data);
  };

  return (
    <M.Grid className={classes.root}>
      <M.Grid className={classes.head}>
        <M.Typography className={classes.title}>Confirmar Envio</M.Typography>
        <M.IconButton onClick={() => toast.remove()}>
          <CloseIcon />
        </M.IconButton>
      </M.Grid>
      <M.Divider />
      <M.Grid className={classes.container}>
        <M.Typography>
          Tem certeza de que deseja criar a notificação{' '}
          <span style={{ fontWeight: 'bold' }}>
            {data.title}? Uma vez criada, ela será imediatamente enviada para {group.toLowerCase()}.
          </span>
        </M.Typography>
      </M.Grid>
      <M.Grid className={classes.btnForm}>
        <M.Button variant="outlined" className={classes.btnCancel} onClick={() => toast.remove()}>
          <CloseIcon />
          CANCELAR
        </M.Button>
        <M.Button variant="outlined" className={classes.btnSend} onClick={confirmButton}>
          <CheckIcon />
          CONFIRMAR
        </M.Button>
      </M.Grid>
    </M.Grid>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ConfirmNotification);
