/* eslint-disable jsx-a11y/alt-text */
import { useEffect, useState } from 'react';
import PhoneOutlinedIcon from '@material-ui/icons/StayCurrentPortraitOutlined';
import WorkOffIcon from '@material-ui/icons/WorkOff';
import HowToRegIcon from '@material-ui/icons/HowToReg';
import lodash from 'lodash';
import api from '../../../services/api';
import images from '../../../assets/imgs';

const iconFormat = {
  height: 32,
  width: 32,
};

export const useDashboard = () => {
  const [dashboardData, setDashboardData] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const showDashboard = () => {
    api
      .get('/users/quantities')
      .then((response) => {
        setIsLoading(false);
        const data = response?.data.data;
        const dashboard = [
          {
            id: 1,
            quantities: data.countTotal,
            icon: <PhoneOutlinedIcon style={iconFormat} />,
            text: 'Total de',
            textWeight: 'usuários do app',
            filteredLink: '',
          },
          {
            id: 2,
            quantities: data.countOfNotClients,
            icon: <WorkOffIcon style={iconFormat} />,
            text: 'Usuários',
            textWeight: 'não clientes',
            filteredLink: '/naoClientes',
          },
          {
            id: 3,
            quantities: data.countOfNotConfirmed,
            icon: <img src={images.clientBlocked} style={iconFormat} />,
            text: 'Clientes',
            textWeight: 'não confirmados',
            filteredLink: '/clientesNaoValidados',
          },
          {
            id: 4,
            quantities: data.countOfAllRightUsers,
            icon: <HowToRegIcon style={iconFormat} />,
            text: 'Clientes',
            textWeight: 'confirmados',
            filteredLink: '/clientesValidados',
          },
        ];
        const orderData = lodash.orderBy(dashboard, ['id'], ['asc']);
        setDashboardData(orderData);
      })
      .catch((err) => {
        setIsLoading(false);
        if (err.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  useEffect(() => {
    showDashboard();
  }, []);

  return {
    dashboardData,
    isLoading,
  };
};
