import lodash from 'lodash';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { useLog } from '../../../hooks/useLog';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatCurrencyBRL from '../../../utils/helpers/formatCurrencyBRL';

export const useLimits = () => {
  const [limites, setLimites] = useState([]);
  const [email, setEmail] = useState([]);
  const [emailCC, setEmailCC] = useState([]);
  const [emailCCO, setEmailCCO] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showLimits = () => {
    api.get('/limit-requests').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((limite) => ({
          dateOrder: `${limite.date_request} - ${limite.hour_request ? limite.hour_request : '99:99'}`,
          id: limite.id,
          day: `${moment(limite.date_hour_request).format('DD/MM/YYYY - HH:mm')}`,
          name: limite.name ? limite.name : '',
          value: limite.new_limit ? formatCurrencyBRL(limite.new_limit) : 'R$ 0.00',
          status: limite.request_status
            ? limite.request_status === 'under analysis'
              ? 'Pendente'
              : limite.request_status === 'finished' && limite.credit_granted === 'yes'
              ? 'Aprovado'
              : 'Reprovado'
            : '',
        }));
        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setLimites(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'Inform a valid id':
            toast.error('Informe um evento valido.');
            break;
          case 'No itens found.':
            toast.error('Nenhum item encontrado.');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const showEmail = () => {
    api.get('/limit-requests/emails/cc').then(
      (response) => {
        setEmail(response.data.data[1].emails.de);
        setEmailCC(response.data.data[1].emails.cc);
        setEmailCCO(response.data.data[1].emails.cco);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Not data found':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'An internal error has ocurred':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao buscar informações de E-MAIL! Tente novamente.');
        }
      },
    );
  };

  const handleEdit = ({ de, cc, cco, handleClose }) => {
    const emails = de.split(';');
    const emailsCC = cc === '' ? [] : cc.split(';');
    const emailsCCO = cco === '' ? [] : cco.split(';');
    api
      .put('/limit-requests/emails/cc', {
        emails_de: emails,
        emails_cc: emailsCC,
        emails_cco: emailsCCO,
      })
      .then(
        (response) => {
          handleClose();
          toast.success('Informações registradas com sucesso!');
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Not data found':
              toast.error('Dados não encontrados nesta requisição!');
              break;
            case 'An internal error has ocurred':
              toast.error('Ocorreu um erro interno! Tente novamente.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Falha ao registrar informações! Tente novamente.');
          }
        },
      );
  };

  const changeStatus = ({ id, newStatus, handleClose, log }) => {
    const msgApprovedOrDenied = newStatus === 'yes' ? 'aprovado' : 'reprovado';

    api
      .post(`/limit-requests/changestatus`, {
        limit_request_id: id,
        credit_granted: newStatus,
        request_status: 'finished',
      })
      .then(
        (response) => {
          sendLog(
            `O status da revisão de limite do cliente ${log.name} no valor de ${log.value} foi alterado de “Pendente” para “${msgApprovedOrDenied}”.`,
          );
          handleClose();
          toast.success(`Status alterado com sucesso, limite ${msgApprovedOrDenied}`);
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Limit request id cannot be empty':
              toast.error('O pedido de limite não pode estar vazio.');
              break;
            case 'Credit granted cannot be empty.':
              toast.error('O crédito concedido não pode estar vazio.');
              break;
            case 'credit granted" must be yes or no':
              toast.error('O crédito concedido deve ser Aprovado ou Reprovado');
              break;
            case 'Limit request has already been finished.':
              toast.error('A solicitação de limite já foi concluída');
              break;
            case 'Limit request not found.':
              toast.error('Pedido de limite não encontrado.');
              break;
            case 'A internal error has ocurred.':
              toast.error('Ocorreu um erro interno.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Ocorreu um erro ao efetuar a alteração na revisão de limite');
          }
        },
      );
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Pedido_Em: item.day,
      Nome: item.name,
      Valor: item.value,
      Status: item.status,
    }));
    downloadXlsx(data, 'Pedidos de revisão de limite.xlsx');
  };

  useEffect(() => {
    showLimits();
    showEmail();
  }, []);

  return {
    limites,
    email,
    emailCC,
    emailCCO,
    handleEdit,
    changeStatus,
    isLoading,
    downloadExcel,
  };
};
