/* eslint-disable camelcase */
import { useEffect, useState } from 'react';
import lodash from 'lodash';
import { toast } from 'react-hot-toast';
import moment from 'moment';
import TooltipUi from '../../../components/Tooltip';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';

const formCurrency = new Intl.NumberFormat('pt-BR', {
  style: 'currency',
  currency: 'BRL',
  minimumFractionDigits: 2,
});

export const useVendas = (filteredStatus) => {
  const [notifications, setNotifications] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const endpointFilteredOptions = filteredStatus
    ? `grain-table/automatic-orders?status=${filteredStatus}`
    : 'grain-table/automatic-orders';

  const showVendas = () => {
    api
      .get(endpointFilteredOptions)
      .then((response) => {
        setIsLoading(false);
        const data = response.data.data.map(
          ({
            created_at: createdAt,
            city_id: cityId,
            identification,
            name,
            culture_name: cultureName,
            delivery_place: deliveryPlace,
            price,
            quantity,
            total_price: totalPrice,
            order_number: OrderNUmber,
            status,
            id,
            end_date,
            start_date,
            email,
            sap_telephone: fone,
          }) => {
            const sapTelephone = formatPhoneNumber(fone);
            return {
              orderDate: moment(createdAt).format('DD/MM/YYYY - HH:mm'),
              id,
              identification: (
                <TooltipUi
                  text={
                    identification.length === 11
                      ? identification.replace(/(\d{3})(\d{3})(\d{3})(\d{2})/g, '$1.$2.$3-$4')
                      : identification.replace(/(\d{2})(\d{3})(\d{3})(\d{4})(\d{2})/g, '$1.$2.$3/$4-$5')
                  }
                  limitCharacters={15}
                  type
                  data={{ email, sapTelephone, name }}
                />
              ),
              name,
              culture_name: cultureName,
              delivery_place: deliveryPlace,
              price: formCurrency.format(price),
              quantity,
              total_price: formCurrency.format(totalPrice),
              end_date: moment(end_date).format('DD/MM/YYYY'),
              start_date: moment(start_date).format('DD/MM/YYYY'),
              status,
            };
          },
        );
        const orderData = lodash.orderBy(data, ['created_at'], ['desc']);
        setNotifications(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.message === 'Request failed with status code 422') {
          setNotifications([]);
        } else {
          setNotifications([]);
        }
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  useEffect(() => {
    showVendas();
  }, [filteredStatus]);

  return {
    notifications,
    isLoading,
  };
};
