import lodash from 'lodash';
import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { useLog } from '../../../hooks/useLog';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';

export const usePestTable = () => {
  const [produtos, setProdutos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showPestTable = () => {
    api.get('/pest-control').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.result.map((produto) => ({
          name: produto.name,
          keyWord: produto.keyword,
          url: produto.url,
        }));
        const orderData = lodash.orderBy(data, ['name'], ['asc']);
        setProdutos(orderData);
      },
      (error) => {
        setIsLoading(false);

        switch (error.response.data.errors[0].message) {
          case 'This pest control not exists.':
            toast.error('Controle de pragas não existe. Por favor, verifique!');
            break;
          case 'This pest control is already registered.':
            toast.error('Controle de pragas já registrado. Por favor, verifique!');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao buscar Pragas');
        }
      },
    );
  };

  const onSubmitPestTable = (newPest, handleClose) => {
    api.post('/pest-control', newPest).then(
      (response) => {
        sendLog(`A Praga/Doença “${newPest.name}” foi adicionada.`);
        toast.success('Cadastro realizado com sucesso!');
        handleClose();
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'No data found.':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'This pest control not exists.':
            toast.error('Controle de pragas não existe. Por favor, verifique!');
            break;
          case 'This pest control is already registered.':
            toast.error('Controle de pragas já registrado. Por favor, verifique!');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao incluir registro. Atualize a página e tente novamente.');
        }
      },
    );
  };

  const editPest = (newPest, handleClose) => {
    api.put(`/pest-control`, newPest).then(
      (response) => {
        sendLog(`A URL da Praga/Doença “${newPest.name}” foi alterada.`);
        handleClose();
        toast.success('Alteração realizada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'No data found.':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'This pest control not exists.':
            toast.error('Controle de pragas não existe. Por favor, verifique!');
            break;
          case 'This pest control is already registered.':
            toast.error('Controle de pragas já registrado. Por favor, verifique!');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao editar registro. Atualize a página e tente novamente.');
        }
      },
    );
  };

  const deletePest = (pest, handleClose) => {
    api.delete('/pest-control', pest).then(
      (response) => {
        sendLog(`A Praga/Doença “${pest.name}” foi excluída.`);
        toast.success('Exclusão realizada com sucesso!');
        handleClose();
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'No data found.':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'This pest control not exists.':
            toast.error('Controle de pragas não existe. Por favor, verifique!');
            break;
          case 'This pest control is already registered.':
            toast.error('Controle de pragas já registrado. Por favor, verifique!');
            break;
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao excluir registro. Atualize a página e tente novamente.');
        }
      },
    );
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Praga_Doenca: item.name,
      Palavra_chave: item.keyWord,
      url: item.url,
    }));

    downloadXlsx(data, 'Praga/Doença cadastradas.xlsx');
  };

  useEffect(() => {
    showPestTable();
  }, []);

  return {
    produtos,
    onSubmitPestTable,
    editPest,
    deletePest,
    isLoading,
    downloadExcel,
  };
};
