import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import lodash from 'lodash';
import moment from 'moment';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';

export const useUsers = (filteredLink) => {
  const [users, setUsers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const showUsers = () => {
    const filteredLinkOptions = {
      naoClientes: '/users/filter?sap_user=false&validated_telephone=false',
      clientesNaoValidados: '/users/filter?sap_user=true&validated_telephone=false',
      clientesValidados: '/users/filter?sap_user=true&validated_telephone=true',
    };

    const endpoint = filteredLink ? filteredLinkOptions[filteredLink] : '/users';
    api.get(endpoint).then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((usuario) => ({
          dateOrder: usuario.creation_date
            ? `${usuario.creation_date} - ${usuario.creation_hour ? usuario.creation_hour : '99:99'}`
            : `${moment('09/01/2021').format('DD/MM/YYYY')}`,
          day: usuario.creation_date
            ? `${moment(usuario.creation_date).format('DD/MM/YYYY')} ${
                usuario.creation_hour ? `- ${usuario.creation_hour}` : ''
              }`
            : `${moment('09/01/2021').format('DD/MM/YYYY')}`,
          idSap: usuario.sap_id_client ? `${usuario.sap_id_client}` : 'usuário não cadastrado no SAP',
          name: usuario.name ? usuario.name : '',
          login: usuario.email ? usuario.email : '',
          phone: usuario.sap_telephone ? formatPhoneNumber(usuario.sap_telephone) : '',
        }));
        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setUsers(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const downloadExcel = (exportAuxRows) => {
    const filteredLinkOptions = {
      naoClientes: 'Usuários Lavoro não clientes.xlsx',
      clientesNaoValidados: 'Usuários Lavoro mais não validados.xlsx',
      clientesValidados: 'Usuários Lavoro validados.xlsx',
    };

    const textFile = filteredLink ? filteredLinkOptions[filteredLink] : 'Usuários Lavoro.xlsx';

    const data = exportAuxRows.map((item) => ({
      Cadastrado_Em: item.day,
      Nome: item.name,
      Login: item.login,
      ID_SAP: item.idSap,
      Telefone: item.phone,
    }));
    downloadXlsx(data, textFile);
  };

  useEffect(() => {
    showUsers();
  }, [filteredLink]);

  return {
    users,
    isLoading,
    downloadExcel,
  };
};
