import { all, takeLatest, call, put } from 'redux-saga/effects';
import CryptoJS from 'crypto-js';
import api from '../../services/api';
import AUTH_ACTIONS_REQUEST from './types';
import MESSAGE_ACTION from '../message/types';

export function* login(action) {
  const username = action.payload?.data?.username;
  const password = action.payload?.data?.password;
  const isRememberAccess = action.payload?.data?.isRememberAccess;

  try {
    yield put({ type: AUTH_ACTIONS_REQUEST.REQUEST });

    const { data } = yield call(api.post, 'auth/backoffice', { username, password });

    yield put({
      type: AUTH_ACTIONS_REQUEST.LOGIN_SUCCESS,
      payload: {
        data: {
          token: data.token,
          isRememberAccess,
          username: `${CryptoJS.AES.encrypt(username, 'lavoroagro-portal')}`,
          password: `${CryptoJS.AES.encrypt(password, 'lavoroagro-portal')}`,
        },
      },
    });
  } catch (error) {
    yield put({ type: AUTH_ACTIONS_REQUEST.REQUEST_ERROR });
    yield put({
      type: MESSAGE_ACTION.OPEN_MESSAGE,
      payload: {
        message: 'Login ou senha inválido. Tente novamente.',
      },
    });
  }
}

export default all([takeLatest(AUTH_ACTIONS_REQUEST.LOGIN, login)]);
