import { useEffect, useState } from 'react';
import lodash from 'lodash';
import moment from 'moment';
import { toast } from 'react-hot-toast';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import { useLog } from '../../../hooks/useLog';

export const useAccess = () => {
  const [access, setAccess] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showAccess = () => {
    api
      .get('/users/backoffice')
      .then((response) => {
        setIsLoading(false);
        const data = response.data.data.map(({ created_at: createdAt, role, email, name }) => ({
          created_at: moment(createdAt).format('DD/MM/YYYY - HH:mm'),
          role,
          email,
          name,
        }));
        const orderData = lodash.orderBy(data, ['createdAt'], ['desc']);
        setAccess(orderData);
      })
      .catch((err) => {
        setIsLoading(false);
        if (err.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const onSubmitAccess = (newAccess, closeModal) => {
    if (newAccess.password.length < 8) {
      toast.error('A senha deve conter pelo menos 8 caracteres.');
    } else {
      api
        .post('/users/backoffice', newAccess)
        .then(() => {
          sendLog(`O acesso do(a) ${newAccess.name} foi adicionado`);
          closeModal();
          toast.success('Acesso de administrador criado com sucesso!');
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        })
        .catch(() => {
          toast.error('Ocorreu um erro no envio do acesso. Tente novamente mais tarde. ');
        });
    }
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map(({ created_at: createdAt, role, email, name }) => ({
      Data_Hora: createdAt,
      Nome: name,
      Permissão: role,
      Email: email,
    }));

    downloadXlsx(data, 'Usuários na base.xlsx');
  };

  useEffect(() => {
    showAccess();
  }, []);

  return {
    access,
    isLoading,
    onSubmitAccess,
    downloadExcel,
  };
};
