import * as M from '@material-ui/core';
import VerifiedUserOutlinedIcon from '@material-ui/icons/VerifiedUserOutlined';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import TableProdutos from './components/Produtos/TableProdutos';
import { usePestTable } from './hooks/usePestTable';
import { useStyles } from './styles';

function ProdutosSintomas() {
  const { produtos, isLoading } = usePestTable();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <VerifiedUserOutlinedIcon className={classes.img} />
          <M.Typography className={classes.titlePage}>Tabela de pragas</M.Typography>
          <M.Divider light className={classes.Divider} />
          <TableProdutos produtos={produtos} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ProdutosSintomas);
