import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/FileCopyOutlined';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import { toast } from 'react-hot-toast';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useStyles } from './styles';

function MessageModal({ msg }) {
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const closeModal = () => setModalIsOpen(false);

  const classes = useStyles();

  const shortText = `${msg.slice(0, 113)}...`;

  const copyToClipboard = () => {
    navigator.clipboard.writeText(msg);
    toast.success('Texto Copiado');
  };

  return (
    <>
      <M.Button onClick={() => setModalIsOpen(true)} className={classes.btnMessage}>
        <M.Typography color="primary">{shortText}</M.Typography>
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid className={classes.head}>
            <M.Typography className={classes.titlePage}>Mensagem Enviada</M.Typography>
            <M.IconButton onClick={() => setModalIsOpen(false)}>
              <CloseIcon />
            </M.IconButton>
          </M.Grid>
          <M.Divider className={classes.divider} />
          <M.Grid className={classes.container}>
            <M.Typography>{msg}</M.Typography>
          </M.Grid>
          <M.Grid className={classes.btnForm}>
            <M.Button variant="outlined" className={classes.btnCancel} onClick={closeModal}>
              <CloseIcon />
              FECHAR
            </M.Button>
            <M.Button variant="outlined" className={classes.btnSend} onClick={copyToClipboard}>
              <CheckIcon />
              COPIAR TEXTO
            </M.Button>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(MessageModal);
