import * as M from '@material-ui/core';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import { useState } from 'react';
import { useForm } from 'react-hook-form';
import { connect } from 'react-redux';
import { usePestTable } from '../../../hooks/usePestTable';
import { useStyles } from './styles';

function AddProduto() {
  const { onSubmitPestTable } = usePestTable();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const { register, handleSubmit } = useForm();

  const onSubmit = (newPest) => {
    onSubmitPestTable(newPest, handleClose);
  };

  return (
    <>
      <M.Button variant="outlined" className={classes.btn} onClick={handleOpen}>
        <AddCircleOutlineIcon className={classes.btnIcon} /> NOVO
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid item className={classes.textContainer}>
            <M.Typography className={classes.title}>Adicionar Praga/Doença</M.Typography>
            <M.Divider className={classes.divider} />
          </M.Grid>
          <form noValidate onSubmit={handleSubmit(onSubmit)}>
            <M.Grid
              container
              direction="row"
              justifyContent="space-around"
              alignItems="center"
              className={classes.container}
            >
              <M.TextField
                reValidateMode="onChange"
                className={classes.input}
                inputProps={{ maxLength: 100 }}
                label="Praga/doença"
                variant="outlined"
                color="secondary"
                type="text"
                labelWidth={105}
                {...register('name', {})}
              />
              <M.TextField
                className={classes.input}
                label="Palavras Chave"
                variant="outlined"
                color="secondary"
                type="text"
                labelWidth={105}
                {...register('keyword', {})}
              />
              <M.TextField
                className={classes.input2}
                label="URL"
                variant="outlined"
                color="secondary"
                type="text"
                labelWidth={30}
                {...register('url', {})}
              />
              <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
                <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                  <CloseIcon className={classes.iconBtn2} />
                  CANCELAR
                </M.Button>
                <M.Button variant="outlined" className={classes.btnYes} type="submit">
                  <CheckIcon className={classes.iconBtn2} />
                  CONFIRMAR
                </M.Button>
              </M.Grid>
            </M.Grid>
          </form>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(AddProduto);
