import * as M from '@material-ui/core';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import InclusaoTable from './components/InclusaoTable/index';
import { useInclusao } from './hooks/useInclusao';
import { useStyles } from './styles';

function InclusaoSap() {
  const { users, isLoading } = useInclusao();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <PersonAddIcon className={classes.img} />
          <M.Typography className={classes.titlePage}>Solicitação de Inclusão no SAP</M.Typography>
          <M.Divider light className={classes.Divider} />
          <InclusaoTable usuarios={users} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(InclusaoSap);
