/* eslint-disable camelcase */
import { useEffect, useState } from 'react';
import lodash, { set } from 'lodash';
import { toast } from 'react-hot-toast';
import moment from 'moment';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';

export const useLocais = (filteredStatus) => {
  const [locais, setLocais] = useState([]);
  const [isLoading, setIsLoading] = useState(true);
  const [listAgroBoard, setListAgroboard] = useState([]);
  const [errorVazio, setErrorVazio] = useState(false);

  const { sendLog } = useLog();

  const ListLocais = () => {
    api
      .get('/grain-table/list-locations/raw')
      .then((response) => {
        setListAgroboard(response.data.data);
      })
      .catch((error) => {
        console.log('error');
        setListAgroboard([]);
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const onSubmitLocal = (data) => {
    api
      .post('grain-table/location', data)
      .then((response) => {
        sendLog(`o Local com o id “${data.id}” foi criada.`);
        toast.success('Unidade adicionada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        if (error.response.data.errors[0].message === 'Location already registered.') {
          toast.error('Esse local ja foi cadastrado');
        } else {
          toast.error('Ocorreu um erro na adição da unidade. Tente novamente mais tarde.');
        }
      });
  };

  const showVendas = () => {
    api
      .get('/grain-table/list-locations')
      .then((response) => {
        setIsLoading(false);
        const data = response.data.data.map(
          ({ city_id, created_at, email, name, name_full, name_raw, state_abbreviation, user_name }) => {
            return {
              orderDate: moment(created_at).format('DD/MM/YYYY - HH:mm'),
              city_id,
              name,
              name_raw,
              state_abbreviation,
              user_name,
              name_full,
            };
          },
        );
        const orderData = lodash.orderBy(data, ['created_at'], ['desc']);
        setLocais(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.response.data.errors[0].message === 'No itens found.') {
          toast.error(`Não há resultados`);
          setErrorVazio(true);
        } else {
          toast.error(`Não há resultados`);
        }
      });
  };

  const DeleteLocal = (id) => {
    api
      .delete(`grain-table/location/${id}`)
      .then((response) => {
        sendLog(`o local com id “${id}” foi deletado`);
        toast.success('Unidade deletada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        toast.error('Ocorreu um erro ao excluir a unidade. Tente novamente mais tarde.');
      });
  };

  useEffect(() => {
    showVendas();
    ListLocais();
  }, [filteredStatus]);

  return {
    locais,
    isLoading,
    DeleteLocal,
    ListLocais,
    listAgroBoard,
    onSubmitLocal,
    errorVazio,
  };
};
