import * as M from '@material-ui/core';
import { useStyles } from './styles';

export function CardVazio({ text, icon }) {
  const classes = useStyles();
  return (
    <M.Paper justifyContent="center" alignItems="center" className={classes.root}>
      <M.Grid container direction="row" justifyContent="center" alignItems="center">
        <img src={icon} alt="logo" alignItems="center" className={classes.img} />
      </M.Grid>
      <M.Grid className={classes.containerText}>
        <M.Typography className={classes.text}>{text}</M.Typography>
      </M.Grid>
    </M.Paper>
  );
}
