import * as M from '@material-ui/core';
import PersonAddIcon from '@material-ui/icons/PersonAdd';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import toast from 'react-hot-toast';
import { connect } from 'react-redux';
import { useStyles } from './styles';
import { useAccess } from '../../hooks/useAccess';

function AddAccess() {
  const [name, setName] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [showPassword, setShowPassword] = useState(false);

  const classes = useStyles();

  const { onSubmitAccess } = useAccess();

  const closeModal = () => {
    setName('');
    setEmail('');
    setPassword('');
    setModalIsOpen(false);
    toast.remove();
  };

  const sendForm = (e) => {
    e.preventDefault();

    const newAccess = {
      name,
      email,
      password,
      confirm_password: password,
    };

    onSubmitAccess(newAccess, closeModal);
  };

  return (
    <>
      <M.Button variant="outlined" className={classes.btnAddNotification} onClick={() => setModalIsOpen(true)}>
        <PersonAddIcon className={classes.imgAddCircle} />
        ADICIONAR USUÁRIO
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.titlePage}>Adicionar administrador</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider className={classes.divider} />
            <form onSubmit={sendForm} method="post" className={classes.form}>
              <M.FormControl className={classes.title} variant="outlined" required>
                <M.InputLabel color="secondary">Nome do Administrador</M.InputLabel>
                <M.OutlinedInput
                  value={name}
                  color="secondary"
                  onChange={(event) => setName(event.target.value)}
                  labelWidth={180}
                  required
                />
              </M.FormControl>
              <M.Grid className={classes.access}>
                <M.FormControl className={classes.fieldAccess} variant="outlined" required>
                  <M.InputLabel color="secondary">Login de acesso (e-mail)</M.InputLabel>
                  <M.OutlinedInput
                    required
                    type="email"
                    value={email}
                    color="secondary"
                    onChange={(event) => setEmail(event.target.value)}
                    labelWidth={185}
                  />
                </M.FormControl>
                <M.FormControl className={classes.fieldAccess} variant="outlined" required>
                  <M.InputLabel color="secondary">Senha</M.InputLabel>
                  <M.OutlinedInput
                    type={showPassword ? 'text' : 'password'}
                    value={password}
                    color="secondary"
                    onChange={(event) => setPassword(event.target.value)}
                    endAdornment={
                      <M.InputAdornment position="end">
                        <M.IconButton
                          aria-label="toggle password visibility"
                          onClick={() => setShowPassword(!showPassword)}
                          edge="end"
                          className={classes.eye}
                        >
                          {showPassword ? <Visibility /> : <VisibilityOff />}
                        </M.IconButton>
                      </M.InputAdornment>
                    }
                    labelWidth={55}
                  />
                </M.FormControl>
              </M.Grid>
              <M.Grid className={classes.btnForm}>
                <M.Button variant="outlined" className={classes.btnCancel} onClick={closeModal}>
                  <CloseIcon />
                  CANCELAR
                </M.Button>
                <M.Button variant="outlined" className={classes.btnSend} type="submit">
                  <CheckIcon />
                  CONFIRMAR
                </M.Button>
              </M.Grid>
            </form>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(AddAccess);
