import * as M from '@material-ui/core';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useStyles } from './styles';

function ShowImage({ text, image }) {
  const classes = useStyles();

  const [modalIsOpen, setModalIsOpen] = useState(false);

  return (
    <>
      <M.Button onClick={() => setModalIsOpen(true)} className={classes.linkImage}>
        {text}
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.title}>Imagem</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Grid
              container
              direction="row"
              justifyContent="space-around"
              alignItems="center"
              className={classes.container}
            >
              <img src={image} alt={text} className={classes.image} />
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ShowImage);
