import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import GavelOutlinedIcon from '@material-ui/icons/GavelOutlined';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import { connect } from 'react-redux';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { useVendas } from '../../hooks/useVendas';
import { useStyles } from './styles';

function ChangeStatusMesaDeGraos({ id, orderNumero, statusAtual }) {
  const { changeStatusVenda } = useVendas();
  const classes = useStyles();

  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [status, setStatus] = useState('');
  const [aviso, setAviso] = useState(false);

  const handleChange = (event) => {
    if (event.target.value === 'Cancelado' || event.target.value === 'Finalizado') {
      setAviso(true);
      setStatus(event.target.value);
    } else {
      setAviso(false);
      setStatus(event.target.value);
    }
  };

  const confirmButton = (idNotification) => {
    setModalIsOpen(false);
    changeStatusVenda(idNotification, orderNumero, status);
  };

  const msgStatusError = `"${status}"`;

  return (
    <>
      <M.IconButton onClick={() => setModalIsOpen(true)} className={classes.btnDelete}>
        <GavelOutlinedIcon />
      </M.IconButton>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography style={{ fontWeight: 'bold' }} className={classes.title}>
                Alteração de status
              </M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider />
            <M.Grid className={classes.container}>
              <M.Typography>
                Status atual: <span style={{ fontWeight: 'bold' }}>{statusAtual}</span>
              </M.Typography>
            </M.Grid>
            <M.Grid style={{ display: 'flex' }}>
              <M.Typography>Novo status:</M.Typography>
              <FormControl variant="standard" fullWidth>
                <InputLabel id="demo-simple-select-label">Selecione...</InputLabel>
                <Select
                  labelId="demo-simple-select-label"
                  id="demo-simple-select"
                  value={status}
                  label="Selecione..."
                  onChange={handleChange}
                >
                  <MenuItem value="Contrato Gerado">Contrato Gerado</MenuItem>
                  <MenuItem value="Contrato Assinado">Contrato Assinado</MenuItem>
                  <MenuItem value="Aprovado">Aprovado</MenuItem>
                  <MenuItem value="Expirado">Expirado</MenuItem>
                  <MenuItem value="Cancelado">Cancelado</MenuItem>
                  <MenuItem value="Finalizado">Finalizado</MenuItem>
                </Select>
              </FormControl>
            </M.Grid>
            <M.Grid className={classes.msgStatusError} style={{ display: `${aviso ? 'block' : 'none'}`}}>
              <M.Typography style={{ color: '#E73434' }}>
                ⚠️ <span className={classes.msgAtention}>Atenção:</span>Ao fazer a alteração para{' '}
                {msgStatusError.toLocaleLowerCase()}, não será mais possível fazer outras mudanças desta solicitação no
                futuro.
              </M.Typography>
            </M.Grid>
            <M.Grid className={classes.btnForm}>
              <M.Button variant="outlined" className={classes.btnCancel} onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnSend} onClick={() => confirmButton(id)}>
                <CheckIcon />
                SALVAR ALTERAÇÃO
              </M.Button>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ChangeStatusMesaDeGraos);
