import { useEffect, useState } from 'react';
import lodash from 'lodash';
import { toast } from 'react-hot-toast';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import { useLog } from '../../../hooks/useLog';

export const useCampanha = () => {
  const [campanhas, setCampanhas] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showCampanhas = () => {
    api
      .get('/banners/backoffice')
      .then((response) => {
        setIsLoading(false);
        const orderData = lodash.orderBy(response.data.data, ['created_at'], ['desc']);
        setCampanhas(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const changeStatus = (id, status) => {
    api
      .patch(`/banners/${id}`, { show_in_home: status })
      .then((response) => {
        window.location.reload();
      })
      .catch((error) => {
        if (error.response.data.errors[0].message === 'Limit of banners at home exceeded') {
          toast.error('O número máximo de campanhas exibidas no carrossel foi atingido (5).');
        } else {
          console.log(error);
        }
      });
  };

  const onSubmitCampanha = (newCampanha) => {
    const fd = new FormData();
    fd.append('title', newCampanha.title);
    fd.append('description', newCampanha.description);
    fd.append('start_date', newCampanha.start_date);
    fd.append('end_date', newCampanha.end_date);
    fd.append('is_just_for_clients', newCampanha.is_just_for_clients);
    fd.append('image', newCampanha.image);
    fd.append('link_url', newCampanha.link_url);
    fd.append('show_in_home', newCampanha.show_in_home);
    fd.append('position', newCampanha.position);

    api
      .post('/banners', fd)
      .then((response) => {
        sendLog(`A campanha “${newCampanha.title}” foi criada.`);
        toast.success('A campanha foi cadastrada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        if (error.response.data.errors[0].message === 'Limit of banners at home exceeded') {
          toast.error('O número máximo de campanhas exibidas no carrossel foi atingido (5).');
        } else {
          console.log(error);
        }
      });
  };

  const editCampanha = (id, newCampanha) => {
    api
      .put(`/banners/${id}`, newCampanha)
      .then((response) => {
        sendLog(`A campanha “${newCampanha.title}” foi alterada.`);
        toast.success('A campanha foi editada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => {
        if (error.response.data.errors[0].message === 'Limit of banners at home exceeded') {
          toast.error('O número máximo de campanhas exibidas no carrossel foi atingido (5).');
        } else {
          console.log(error);
        }
      });
  };

  const deleteCampanha = (id, title) => {
    api
      .delete(`/banners/${id}`)
      .then((response) => {
        sendLog(`A campanha “${title}” foi excluída.`);
        toast.success('A campanha foi removida com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => console.log('=====error====', error));
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Titulo_Campanha: item.title,
      Descricao: item.description.props.text,
      Vigencia: item.dateCampanha,
      Imagem: item.image.props.image,
      Publico: item.public,
      url: item.link_url.props.text,
      Exibe_Carrossel: !item.status.checked ? 'Sim' : 'Não',
      Posicao: item.position,
    }));

    downloadXlsx(data, 'Campanhas cadastradas.xlsx');
  };

  useEffect(() => {
    showCampanhas();
  }, []);

  return {
    campanhas,
    isLoading,
    changeStatus,
    onSubmitCampanha,
    editCampanha,
    deleteCampanha,
    downloadExcel,
  };
};
