import { FormControl, Grid, InputLabel, OutlinedInput } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import api from '../../../../services/api';
import { logout } from '../../../../store/auth/actions';
import { openMessage } from '../../../../store/message/actions';
import images from '../../../../assets/imgs';
import { useStyles } from './styles';

function CardRecuperarSenha({ dispatch }) {
  const classes = useStyles();
  const history = useHistory();

  const [email, setEmail] = useState('');

  const handleChange = () => {
    api.post('/users/recoverypassword', { email }).then(
      (response) => {
        dispatch(openMessage('Email de recuperação de senha enviado com sucesso'));
        setEmail('');
        setTimeout(() => {
          history.push('/');
        }, 1000);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Antecipation request id cannot be empty.':
            dispatch(openMessage('O limite do pedido de antecipação não pode estar vazio'));
            break;
          case 'Email cannot be empty.':
            dispatch(openMessage('O campo e-mail não pode estar vazio.'));
            break;
          case 'There was a problem sending message.':
            dispatch(openMessage('Ocorreu um problema ao enviar o e-mail.'));
            break;
          case 'User not registered.':
            dispatch(openMessage('Usuário não registrado.'));
            break;
          case 'A internal error has ocurred.':
            dispatch(openMessage('Ocorreu um erro interno.'));
            break;
          case 'Unauthorized':
            dispatch(openMessage('Token invalido, solicite a alteração de senha novamente'));
            dispatch(logout());
            break;
          case 'The incoming token has expired':
            dispatch(openMessage('Token invalido, efetue o login novamente.'));
            dispatch(logout());
            break;
          case 'Forbidden':
            dispatch(openMessage('Sua sessão expirou, efetue o login novamente.'));
            dispatch(logout());
            break;
          default:
            dispatch(openMessage('Ocorreu um erro ao solicitar a alteração da senha'));
        }
      },
    );
  };

  return (
    <Card className={classes.root} direction="row" alignItems="center">
      <Grid item container direction="row" justifyContent="flex-start" alignItems="center">
        <ArrowBackIcon className={classes.icon} />
        <Typography className={classes.textBlue} onClick={() => history.push('/')}>
          Voltar
        </Typography>
      </Grid>
      <CardContent>
        <Grid container direction="column" justifyContent="center" alignItems="center" spacing={2}>
          <img src={images.coloredLogo} alt="logo" alignItems="center" />
          <Typography className={classes.title}>ESQUECI MINHA SENHA</Typography>
          <FormControl className={classes.input} variant="outlined">
            <InputLabel color="secondary">E-mail</InputLabel>
            <OutlinedInput
              value={email}
              color="secondary"
              onChange={(event) => setEmail(event.target.value)}
              labelWidth={45}
            />
          </FormControl>
        </Grid>
      </CardContent>
      <CardActions>
        <Grid container direction="column" justifyContent="center" alignItems="center">
          <Button variant="contained" size="small" color="primary" className={classes.btn} onClick={handleChange}>
            ENVIAR LINK DE RECUPERAÇÃO
          </Button>
        </Grid>
      </CardActions>
    </Card>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(CardRecuperarSenha);
