import * as M from '@material-ui/core';
import images from '../../../../assets/imgs/index';
import CorfimChanges from '../ConfirmChanges';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import { useUsers } from '../../hooks/useUsers';
import { columns } from './Columns';
import { useStyles } from './styles';

export default function UserTable({ usuarios }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(usuarios);
  const { downloadExcel } = useUsers();
  const classes = useStyles();

  const plural = auxRows.length >= 2 ? 'pedidos de alterações' : 'pedido de alteração';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural,
    downloadExcel,
  };

  return (
    <>
      {usuarios && usuarios.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps} />
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader>
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} align={column.align} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                      {columns.map((column) => {
                        const value = row[column.id];
                        return (
                          <M.TableCell key={column.id} align={column.align}>
                            {column.format && typeof value === 'number' ? column.format(value) : value}
                          </M.TableCell>
                        );
                      })}
                      <M.TableCell className={classes.btnChanges}>
                        <CorfimChanges
                          id={row.id}
                          identification={row.identification}
                          name={row.name}
                          oldPhone={row.oldPhone}
                          newPhone={row.newPhone}
                          oldEmail={row.oldEmail}
                          newEmail={row.newEmail}
                        />
                      </M.TableCell>
                    </M.TableRow>
                  );
                })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <CardVazio text="Não há pedidos efetuados até o momento" icon={images.revisaoCadastroVazio} />
      )}
    </>
  );
}
