import * as M from '@material-ui/core';
import DashboardIcon from '@material-ui/icons/Dashboard';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import DashboardCard from './components/DashboardCard';
import { TEXT_MODULE } from './constants/Text';
import { useDashboard } from './hooks/useDashboard';
import { useStyles } from './styles';

function Dashboard() {
  const classes = useStyles();

  const { dashboardData, isLoading } = useDashboard();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid className={classes.containerTitle}>
            <DashboardIcon className={classes.img} />
            <M.Typography className={classes.titlePage}>{TEXT_MODULE.TITLE}</M.Typography>
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <M.Grid className={classes.dashboard}>
            {dashboardData.map(({ id, quantities, icon, text, textWeight, filteredLink }) => (
              <DashboardCard
                key={id}
                quantities={quantities}
                icon={icon}
                text={text}
                textWeight={textWeight}
                filteredLink={filteredLink}
              />
            ))}
          </M.Grid>
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Dashboard);
