import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useNotification } from '../../hooks/useNotification';
import { useStyles } from './styles';

function DeleteNotification({ id, title }) {
  const { deleteNotification } = useNotification();
  const classes = useStyles();

  const [modalIsOpen, setModalIsOpen] = useState(false);

  const confirmButton = (idNotification) => {
    setModalIsOpen(false);
    deleteNotification(idNotification, title);
  };

  return (
    <>
      <M.IconButton onClick={() => setModalIsOpen(true)} className={classes.btnDelete}>
        <DeleteIcon />
      </M.IconButton>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.title}>Remover Campanha</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider />
            <M.Grid className={classes.container}>
              <M.Typography>
                Tem certeza de que deseja remover a notificação <span style={{ fontWeight: 'bold' }}>{title}</span>?
              </M.Typography>
            </M.Grid>
            <M.Grid className={classes.btnForm}>
              <M.Button variant="outlined" className={classes.btnCancel} onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnSend} onClick={() => confirmButton(id)}>
                <CheckIcon />
                CONFIRMAR
              </M.Button>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(DeleteNotification);
