import * as M from '@material-ui/core';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import { useLimits } from '../../hooks/useLimits';
import ModalLimit from '../ModalLimit';
import { columns } from './Columns';
import { useStyles } from './styles';

export default function LimiteTable({ limites }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(limites);
  const { downloadExcel } = useLimits();
  const classes = useStyles();

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: 'pedidos encontrados',
    downloadExcel,
  };

  return (
    <>
      {limites && limites.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps} />
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table">
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} align={column.align} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                      {columns.map((column) => {
                        const value = row[column.id];
                        if (column.id === 'status') {
                          switch (value) {
                            case 'Aprovado':
                              return (
                                <M.TableCell key={column.id} align={column.align}>
                                  <img src={images.GreenDot} alt="logo" alignItems="center" className={classes.dot} />
                                  {column.format && typeof value === 'number' ? column.format(value) : value}
                                </M.TableCell>
                              );
                            case 'Reprovado':
                              return (
                                <M.TableCell key={column.id} align={column.align}>
                                  <img src={images.RedDot} alt="logo" alignItems="center" className={classes.dot} />
                                  {column.format && typeof value === 'number' ? column.format(value) : value}
                                </M.TableCell>
                              );
                            default:
                              return (
                                <M.TableCell key={column.id} align={column.align}>
                                  <img src={images.YellowDot} alt="logo" alignItems="center" className={classes.dot} />
                                  {column.format && typeof value === 'number' ? column.format(value) : value}
                                </M.TableCell>
                              );
                          }
                        } else {
                          return (
                            <M.TableCell key={column.id} align={column.align}>
                              {column.format && typeof value === 'number' ? column.format(value) : value}
                            </M.TableCell>
                          );
                        }
                      })}
                      <M.TableCell>{row.status === 'Pendente' && <ModalLimit id={row.id} name={row.name} value={row.value} />}</M.TableCell>
                    </M.TableRow>
                  );
                })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <CardVazio text="Sem limites para revisão!" icon={images.RevisaoVazia} />
      )}
    </>
  );
}
