import * as M from '@material-ui/core';
import CalendarTodayIcon from '@material-ui/icons/CalendarToday';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import TableEventos from './components/TableEventos/index';
import { useEvents } from './hooks/useEvents';
import { useStyles } from './styles';

function Eventos() {
  const { eventos, ufOptions, isLoading } = useEvents();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <CalendarTodayIcon className={classes.img} />
          <M.Typography className={classes.titlePage}>Eventos</M.Typography>
          <M.Divider light className={classes.Divider} />
          <TableEventos ufOptions={ufOptions} eventos={eventos} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Eventos);
