import * as M from '@material-ui/core';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import CheckIcon from '@material-ui/icons/Check';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import jwtDecode from 'jwt-decode';
import { store } from '../../../../store';
import { useLocais } from '../../hooks/useLocais';
import { useStyles } from './styles';





function AddLocal() {
  const { listAgroBoard, onSubmitLocal,  } = useLocais();

  const [localDeVenda, setLocalDeVenda] = useState('');
  const [agroBoard, setAgroboard] = useState('');
  const [error, setError] = useState(false);
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [errorIsOpen, setErrorIsOpen] = useState(false);


  const token = store.getState().auth?.token;
  const decode = jwtDecode(token);
  const userEmail = decode['cognito:username'];

  const styleProps = { display: errorIsOpen ? 'flex' : 'none' };

  const classes = useStyles(styleProps);

  const handleChange = (event) => {
    setAgroboard(event.target.value)
    setError(false)
  };

  const handleClose = () => {
    setLocalDeVenda('');
    setLocalDeVenda('')
    setError(false)
    setModalIsOpen(false);
  };

  const sendForm = (e) => {
    e.preventDefault();
   if(localDeVenda.trim() === '') {
    setError(true)
   }else if(agroBoard === ''){
    setError(true)
   }else {
    const data = {
      email: userEmail,
      city_id: agroBoard,
      name_full: localDeVenda
    };
    onSubmitLocal(data);
    handleClose()
    setError(false)
   }
  };

  return (
    <>
      <M.Button variant="outlined" className={classes.btnAddCampanha} onClick={() => setModalIsOpen(true)}>
        <AddCircleOutlineIcon className={classes.imgAddCircle} />
        NOVO LOCAL
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.titlePage}>Adicionar Local</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Grid>
              <M.Divider className={classes.divider} />
              </M.Grid>
              <M.Grid style={{display: `${error ? 'flex' : 'none'}`}} className={classes.warning}>
                <M.Typography className={classes.textWarning}>Todos os campos devem ser preenchidos!</M.Typography>
              </M.Grid>
            <M.Grid className={classes.container}>
              <M.Grid>
                <form encType="multipart/form-data" onSubmit={sendForm} method="post" className={classes.form}>
                <M.Grid>
            <FormControl  fullWidth>
              <InputLabel id="demo-simple-select-label">Cadastro no Agroboard</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={agroBoard}
                label="Cadastro no agroboard"
                onChange={handleChange}


              >
                {listAgroBoard.map(item => {
                  return (
                      <MenuItem value={item.city_id}>{item.name_raw}</MenuItem>
                  )
                })}
              </Select>
            </FormControl>
            </M.Grid>
                <M.FormControl className={classes.url} variant="outlined">
                  <M.InputLabel  color="secondary">Local de venda</M.InputLabel>
                  <M.OutlinedInput
                    className={classes.urlInput}
                    value={localDeVenda}
                    color="secondary"
                    onChange={(event) => (setLocalDeVenda(event.target.value) + setError(false))}
                    labelWidth={110}
                    inputProps={{ maxLength: 40 }}
                  />
                </M.FormControl>
                <M.Grid className={classes.btnForm}>
                  <M.Button variant="outlined" className={classes.btnCancel} onClick={handleClose}>
                    <CloseIcon />
                    CANCELAR
                  </M.Button>
                  <M.Button variant="outlined" className={classes.btnSend} type="submit">
                    <CheckIcon />
                    CONFIRMAR
                  </M.Button>
                </M.Grid>
                </form>
              </M.Grid>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(AddLocal);
