import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import CreateIcon from '@material-ui/icons/Create';
import { DatePicker } from '@mui/lab';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import Autocomplete from '@mui/material/Autocomplete';
import { useState } from 'react';
import { connect } from 'react-redux';
import { DropImageInput } from '../../../../components/PickDropImage';
import SwitchUi from '../../../../components/Switch';
import formatDate, { formatDateForEnUS, formatDateForSend } from '../../../../utils/helpers/formatDate';
import { useCampanha } from '../../hooks/useCampanha';
import { useStyles } from './styles';

const publicoAlvoMock = [{ label: 'Clientes Lavoro' }, { label: 'Todos' }];
const positionMock = [{ label: '1' }, { label: '2' }, { label: '3' }, { label: '4' }, { label: '5' }];

function EditCampanha({ campanha }) {
  const { editCampanha } = useCampanha();

  const starDate = formatDateForEnUS(campanha.dateCampanha.slice(0, 10));
  const dateEnd = formatDateForEnUS(campanha.dateCampanha.slice(15));

  const [image, setImage] = useState([]);
  const [title, setTitle] = useState(campanha.title);
  const [initialDate, setInitialDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [description, setDescription] = useState(campanha.description.props.text);
  const [url, setUrl] = useState(campanha.link_url.props.text);
  const [client, setClient] = useState(campanha.public);
  const [showInCarousel, setShowInCarousel] = useState(campanha.status.props.checked);
  const [position, setPosition] = useState(campanha.position);
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const classes = useStyles();

  const cancelEdit = () => {
    setModalIsOpen(false);
    window.location.reload();
  };

  const sendForm = (e) => {
    e.preventDefault();
    const statusClient = client === 'Clientes Lavoro' ? true : false;

    const data = {
      title,
      start_date: initialDate ? formatDate(initialDate) : formatDateForSend(campanha.dateCampanha.slice(0, 10)),
      end_date: endDate ? formatDate(endDate) : formatDateForSend(campanha.dateCampanha.slice(15)),
      description,
      link_url: url,
      is_just_for_clients: statusClient,
      show_in_home: showInCarousel,
      position,
      image,
    };

    if (image.length === 0) {
      const fd = new FormData();
      fd.append('title', data.title);
      fd.append('description', data.description);
      fd.append('start_date', data.start_date);
      fd.append('end_date', data.end_date);
      fd.append('is_just_for_clients', data.is_just_for_clients);
      fd.append('link_url', data.link_url);
      fd.append('show_in_home', data.show_in_home);
      fd.append('position', data.position);

      editCampanha(campanha.id, fd);
    } else {
      const fd = new FormData();
      fd.append('title', data.title);
      fd.append('description', data.description);
      fd.append('start_date', data.start_date);
      fd.append('end_date', data.end_date);
      fd.append('is_just_for_clients', data.is_just_for_clients);
      fd.append('link_url', data.link_url);
      fd.append('show_in_home', data.show_in_home);
      fd.append('position', data.position);
      fd.append('image', data.image);

      editCampanha(campanha.id, fd);
    }
  };

  return (
    <>
      <M.IconButton onClick={() => setModalIsOpen(true)} className={classes.btnCreate}>
        <CreateIcon />
      </M.IconButton>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.titlePage}>Editar Campanha</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider className={classes.divider} />
            <M.Grid className={classes.container}>
              <M.Grid>
                <DropImageInput onFileChange={setImage} image={campanha.image.props.image} />
              </M.Grid>
              <form encType="multipart/form-data" onSubmit={sendForm} method="post" className={classes.form}>
                <M.FormControl className={classes.title} variant="outlined" required>
                  <M.InputLabel color="secondary">Título da Campanha</M.InputLabel>
                  <M.OutlinedInput
                    value={title}
                    color="secondary"
                    onChange={(event) => setTitle(event.target.value)}
                    labelWidth={150}
                  />
                </M.FormControl>
                <M.Grid className={classes.dateContainer}>
                  <M.FormControl className={classes.dateForm}>
                    <DatePicker
                      inputFormat="dd/MM/yyyy"
                      value={initialDate || starDate}
                      onChange={(newValue) => {
                        setInitialDate(newValue);
                      }}
                      renderInput={(params) => (
                        <M.TextField {...params} label="Data de início" required variant="outlined" color="secondary" />
                      )}
                    />
                  </M.FormControl>
                  <M.FormControl className={classes.dateForm}>
                    <DatePicker
                      inputFormat="dd/MM/yyyy"
                      value={endDate || dateEnd}
                      onChange={(newValue) => {
                        setEndDate(newValue);
                      }}
                      renderInput={(params) => (
                        <M.TextField
                          {...params}
                          label="Data de término"
                          required
                          variant="outlined"
                          color="secondary"
                        />
                      )}
                    />
                  </M.FormControl>
                </M.Grid>
                <M.FormControl className={classes.description} variant="outlined" required>
                  <M.InputLabel color="secondary">Descrição</M.InputLabel>
                  <M.OutlinedInput
                    required
                    value={description}
                    color="secondary"
                    onChange={(event) => setDescription(event.target.value)}
                    labelWidth={80}
                    multiline
                    rows={5}
                  />
                </M.FormControl>
                <M.FormControl className={classes.url} variant="outlined">
                  <M.InputLabel color="secondary">URL</M.InputLabel>
                  <M.OutlinedInput
                    className={classes.urlInput}
                    value={url}
                    color="secondary"
                    onChange={(event) => setUrl(event.target.value)}
                    labelWidth={30}
                  />
                </M.FormControl>
                <M.Grid className={classes.setContainer}>
                  <Autocomplete
                    disableClearable
                    id="select-client"
                    value={client}
                    options={publicoAlvoMock}
                    sx={{ width: 170 }}
                    renderInput={(params) => (
                      <M.TextField {...params} label="Público-Alvo" required variant="outlined" color="secondary" />
                    )}
                    onInputChange={(event, newInputValue) => {
                      setClient(newInputValue);
                    }}
                  />
                  <SwitchUi label="Exibir no Carrossel" checked={showInCarousel} changeStatus={setShowInCarousel} />
                  <Autocomplete
                    disablePortal
                    disableClearable
                    id="select-position"
                    value={position}
                    options={positionMock}
                    sx={{ width: 160 }}
                    renderInput={(params) => (
                      <M.TextField {...params} label="Posição" required variant="outlined" color="secondary" />
                    )}
                    onInputChange={(event, newInputValue) => {
                      setPosition(newInputValue);
                    }}
                  />
                </M.Grid>
                <M.Grid className={classes.btnForm}>
                  <M.Button variant="outlined" className={classes.btnCancel} onClick={cancelEdit}>
                    <CloseIcon />
                    CANCELAR
                  </M.Button>
                  <M.Button variant="outlined" className={classes.btnSend} type="submit">
                    <CheckIcon />
                    CONFIRMAR
                  </M.Button>
                </M.Grid>
              </form>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(EditCampanha);
