/* eslint-disable import/order */
import { useState } from 'react';
import * as M from '@material-ui/core';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import DeleteLocalMesaDeGraos from '../DeleteLocalMesaDeGraos';
import { columns } from './Columns';
import { useStyles } from './styles';
import { useLocais } from '../../hooks/useLocais';
import AddLocal from '../AddLocal';

export default function LocalTable() {
  const classes = useStyles();
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();

  const [status, setStatus] = useState('');
  const { locais } = useLocais(status);

  const { searchText, auxRows, requestSearch } = useRequestSearch(locais);

  const plural = auxRows.length >= 2 ? 'locais cadastrados' : 'local cadastrado';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural
  };

  return (
    <>
      {locais && locais.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
             <AddLocal/>
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table" className={classes.table}>
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} style={column.style} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows &&
                  auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    return (
                      <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                        {columns.map((column) => {
                          const value = row[column.id];
                          return (
                            <M.TableCell key={column.id} align={column.align} style={column.style}>
                              {column.format && typeof value === 'number' ? column.format(value) : value}
                            </M.TableCell>
                          );
                        })}
                        <M.TableCell className={classes.btnDelete}>
                          <DeleteLocalMesaDeGraos
                            id={row.city_id}
                            name={row.name_full}
                          />
                        </M.TableCell>
                      </M.TableRow>
                    );
                  })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <M.Grid className={classes.newNotification}></M.Grid>
          <CardVazio text="Não há nenhum local cadastrado!" icon={images.notificationVoid} />
        </>
      )}
    </>
  );
}
