import lodash from 'lodash';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import { useLog } from '../../../hooks/useLog';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';

export const useAntecipacao = () => {
  const [antecipacao, setAntecipacao] = useState([]);
  const [email, setEmail] = useState({});
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showAntecipacao = () => {
    api.get('/antecipation-requests').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.final_data.map((antecipation) => ({
          dateOrder: `${antecipation.date_request} - ${
            antecipation.hour_request ? antecipation.hour_request : '99:99'
          }`,
          id: antecipation.request_id ? antecipation.request_id : '',
          day: antecipation.date_hour_request
            ? `${moment(antecipation.date_hour_request).format('DD/MM/YYYY - HH:mm')}`
            : `${moment(antecipation.date_request).format('DD/MM/YYYY')}`,
          name: antecipation.name ? antecipation.name : '',
          docs: antecipation.documents_count ? antecipation.documents_count : '0',
          phone: antecipation.callback_phone_number ? formatPhoneNumber(antecipation.callback_phone_number) : '',
          payment: antecipation.antecipation_date
            ? `${moment(antecipation.antecipation_date).format('DD/MM/YYYY')}`
            : '',
          status: antecipation.request_status ? antecipation.request_status : '',
        }));
        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setAntecipacao(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break
        }
      },
    );
  };

  const showEmailConfig = () => {
    api.get('/antecipation-requests/emails/cc').then(
      (response) => {
        const data = {
          email: response.data.data[0].emails.emails_de,
          emailCC: response.data.data[0].emails.cc,
          emailCCO: response.data.data[0].emails.cco,
        };
        setEmail(data);
      },
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Not data found':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'An internal error has ocurred':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao buscar informações de E-MAIL! Tente novamente.');
        }
      },
    );
  };

  const handleEdit = ({ de, cc, cco, handleClose }) => {
    const emails = de.split(';');
    const emailsCC = cc === '' ? [] : cc.split(';');
    const emailsCCO = cco === '' ? [] : cco.split(';');
    api
      .put('/limit-requests/emails/cc', {
        emails_de: emails,
        emails_cc: emailsCC,
        emails_cco: emailsCCO,
      })
      .then(
        (response) => {
          handleClose();
          toast.success('Informações registradas com sucesso!');
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Not data found':
              toast.error('Dados não encontrados nesta requisição!');
              break;
            case 'An internal error has ocurred':
              toast.error('Ocorreu um erro interno! Tente novamente.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Falha ao registrar informações! Tente novamente.');
          }
        },
      );
  };

  const approvedAntecipacao = (status, handleClose, log) => {
    api
      .post(`/antecipation-requests/changestatus`, status)
      .then(
        (response) => {
          sendLog(`O status da antecipação de pagamento do cliente ${log.name} para a data ${log.payment} foi alterado de “Pendente” para “Finalizada”.`)
          handleClose();
          toast.success('Status alterado com sucesso.');
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Antecipation request id cannot be empty.':
              toast.error('O limite do pedido de antecipação não pode estar vazio');
              break;
            case 'granted cannot be empty.':
              toast.error('O pedido de antecipação está vazio');
              break;
            case 'granted" must be true or false':
              toast.error('O pedido precisa ser Sim ou Não');
              break;
            case 'Antecipation request has already been finished.':
              toast.error('O pedido de Antecipação já foi finalizado.');
              break;
            case 'Antecipation request not found.':
              toast.error('Pedido de Antecipação não encontrado.');
              break;
            case 'A internal error has ocurred.':
              toast.error('Ocorreu um erro interno.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Ocorreu um erro ao alterar o status');
          }
        },
      );
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Cadastrado_em: item.day,
      Nome: item.name,
      Docs: item.docs,
      Telefone: item.phone,
      Data_pagamento: item.payment,
      Status: item.status,
    }));

    downloadXlsx(data, 'Pedidos de Antecipação Lavoro.xlsx');
  };

  useEffect(() => {
    showAntecipacao();
    showEmailConfig();
  }, []);

  return {
    antecipacao,
    email,
    isLoading,
    approvedAntecipacao,
    handleEdit,
    downloadExcel,
  };
};
