import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import GavelOutlinedIcon from '@material-ui/icons/GavelOutlined';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useUsers } from '../../hooks/useUsers';
import { useStyles } from './styles';

function CorfimChanges({ id, identification, name, oldPhone, newPhone, oldEmail, newEmail }) {
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const classes = useStyles();

  const { changeInfoUser, deleteUserReview } = useUsers();

  const closeModal = () => setModalIsOpen(false);

  const deleteInfoUser = () => {
    deleteUserReview(id, name)
    closeModal()
  }

  const sendForm = () => {
    const data = {
      identification,
      update_registration_request_id: id,
    };

    changeInfoUser(data, name);
  };

  return (
    <>
      <M.Button onClick={() => setModalIsOpen(true)}>
        <GavelOutlinedIcon />
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid className={classes.head}>
            <M.Typography className={classes.titlePage}>Alteração de cadastro</M.Typography>
            <M.IconButton onClick={() => setModalIsOpen(false)}>
              <CloseIcon />
            </M.IconButton>
          </M.Grid>
          <M.Divider className={classes.divider} />
          <M.Grid className={classes.container}>
            <M.Grid>
              <M.Typography className={classes.text}>
                Você confirma a(s) alteração(ões) do(s) dado(s) abaixo?
              </M.Typography>
            </M.Grid>
            <M.Grid>
              <M.Typography className={classes.text}>Telefone:</M.Typography>
              <M.Typography className={classes.text}>De {oldPhone}</M.Typography>
              <M.Typography className={classes.textStrong}>Para {newPhone}</M.Typography>
            </M.Grid>
            <M.Grid>
              <M.Typography className={classes.text}>E-mail:</M.Typography>
              <M.Typography className={classes.text}>De {oldEmail}</M.Typography>
              <M.Typography className={classes.textStrong}>Para {newEmail}</M.Typography>
            </M.Grid>
          </M.Grid>
          <M.Grid className={classes.btnForm}>
            <M.Button variant="outlined" className={classes.btnCancel} onClick={deleteInfoUser}>
              <CloseIcon />
              NÃO
            </M.Button>
            <M.Button variant="outlined" className={classes.btnSend} onClick={sendForm}>
              <CheckIcon />
              SIM
            </M.Button>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(CorfimChanges);
