import lodash from 'lodash';
import moment from 'moment';
import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatCurrencyBRL from '../../../utils/helpers/formatCurrencyBRL';

export const useReagendamentos = () => {
  const [reagendamentos, setReagendamentos] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const showReagendamentos = () => {
    api.get('/historic/reschedule/log').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((reschedule) => ({
          dateOrder: reschedule.date,
          id: reschedule.id ? reschedule.id : '',
          dateSolicitation: moment(reschedule.date).format('DD/MM/YYYY'),
          clientName: reschedule.applicant_name,
          consultantName: reschedule.consultant_name,
          type: reschedule.delivery_type ? 'Confirmada' : 'Estimada',
          scheduledDate: moment(reschedule.scheduled_date).format('DD/MM/YYYY'),
          reschedule: moment(reschedule.reschedule_date).format('DD/MM/YYYY'),
          rescheduleRequest: reschedule.order_number,
          consultant: reschedule.request_status ? reschedule.request_status : '',
          consultantPhone: reschedule.consultant_telephone,
          isWhatsapp: reschedule.reschedule_by_phone,
          consultantEmail: reschedule.consultant_email,
        }));
        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setReagendamentos(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'An internal error has occurred.':
            toast.error(
              'Ops! Ocorreu um erro interno. Tente novamente mais tarde ou contate suporte técnico da aplicação.',
            );
            break;
          case 'No order found for this identification.':
            toast.error('Atenção! Nenhum pedido foi encontrado para esta identificação.');
            break;
          case 'No order found for this property.':
            toast.error('Atenção! Nenhum pedido foi encontrado para esta propriedade.');
            break;
          case 'No product found for this order.':
            toast.error('Atenção! Nenhum produto foi encontrado para este pedido.');
            break;
          case 'No order found for this date.':
            toast.error('Atenção! Nenhum pedido foi encontrado para esta data.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Ocorreu um erro ao buscar os pedidos de Reagendamento');
        }
      },
    );
  };

  const showEmail = () => {
    api.get('/limit-requests/emails/cc').then(
      (response) => {},
      (error) => {
        switch (error.response.data.errors[0].message) {
          case 'Not data found':
            toast.error('Dados não encontrados nesta requisição!');
            break;
          case 'An internal error has ocurred':
            toast.error('Ocorreu um erro interno! Tente novamente.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            toast.error('Falha ao buscar informações de E-MAIL! Tente novamente.');
        }
      },
    );
  };

  const handleEdit = ({ de, cc, cco, handleClose }) => {
    const emails = de.split(';');
    const emailsCC = cc === '' ? [] : cc.split(';');
    const emailsCCO = cco === '' ? [] : cco.split(';');
    api
      .put('/limit-requests/emails/cc', {
        emails_de: emails,
        emails_cc: emailsCC,
        emails_cco: emailsCCO,
      })
      .then(
        (response) => {
          handleClose();
          toast.success('Informações registradas com sucesso!');
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Not data found':
              toast.error('Dados não encontrados nesta requisição!');
              break;
            case 'An internal error has ocurred':
              toast.error('Ocorreu um erro interno! Tente novamente.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Falha ao registrar informações! Tente novamente.');
          }
        },
      );
  };

  const changeStatus = ({ id, newStatus, handleClose }) => {
    const msgApprovedOrDenied = newStatus === 'yes' ? 'aprovado' : 'reprovado';

    api
      .post(`/limit-requests/changestatus`, {
        limit_request_id: id,
        credit_granted: newStatus,
        request_status: 'finished',
      })
      .then(
        (response) => {
          handleClose();
          toast.success(`Status alterado com sucesso, limite ${msgApprovedOrDenied}`);
          setTimeout(() => {
            window.location.reload();
          }, 3000);
        },
        (error) => {
          switch (error.response.data.errors[0].message) {
            case 'Limit request id cannot be empty':
              toast.error('O pedido de limite não pode estar vazio.');
              break;
            case 'Credit granted cannot be empty.':
              toast.error('O crédito concedido não pode estar vazio.');
              break;
            case 'credit granted" must be yes or no':
              toast.error('O crédito concedido deve ser Aprovado ou Reprovado');
              break;
            case 'Limit request has already been finished.':
              toast.error('A solicitação de limite já foi concluída');
              break;
            case 'Limit request not found.':
              toast.error('Pedido de limite não encontrado.');
              break;
            case 'A internal error has ocurred.':
              toast.error('Ocorreu um erro interno.');
              break;
            case 'Unauthorized':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'The incoming token has expired':
              toast.error('Token invalido, efetue o login novamente.');
              break;
            case 'Forbidden':
              toast.error('Sua sessão expirou, efetue o login novamente.');
              break;
            default:
              toast.error('Ocorreu um erro ao efetuar a alteração na revisão de limite');
          }
        },
      );
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Data_Solicitacao: item.dateSolicitation,
      Solicitante: item.clientName,
      Tipo_Entrega: item.type,
      Data_Agendada: item.scheduledDate,
      Reagendamento: item.reschedule,
      Pedido: item.rescheduleRequest,
    }));

    downloadXlsx(data, 'Pedidos de reagendamento.xlsx');
  };

  useEffect(() => {
    showReagendamentos();
  }, []);

  return {
    reagendamentos,
    isLoading,
    downloadExcel,
  };
};
