/* eslint-disable jsx-a11y/alt-text */
import * as M from '@material-ui/core';
import { connect } from 'react-redux';
import { Link } from 'react-router-dom';
import { filteredUsers } from './config';
import { useStyles } from './styles';

function UsersFiltered({ params }) {
  const classes = useStyles();

  return (
    <M.Grid className={classes.root}>
      {filteredUsers.map(({ id, icon, text, filteredLink, linkTo }) => {
        return (
          <Link to={`/usuarios/${linkTo}`} key={id} style={{ textDecoration: 'none' }}>
            <M.Chip label={text} color={filteredLink[params]} icon={icon} style={{ cursor: 'pointer' }} />
          </Link>
        );
      })}
    </M.Grid>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(UsersFiltered);
