import * as M from '@material-ui/core';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import { useReagendamentos } from '../../hooks/useReagendamentos';
import UserData from '../UserData';
import { columns } from './Columns';
import { useStyles } from './styles';

export default function ReschedulingTable({ reagendamentos }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(reagendamentos);
  const { downloadExcel } = useReagendamentos();
  const classes = useStyles();
  console.log(auxRows)

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: 'requisições de reagendamento na base',
    downloadExcel,
  };

  return (
    <>
      {reagendamentos && reagendamentos.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps} />
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table">
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} align={column.align} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell TableCell className={classes.title}>
                    Consultor
                  </M.TableCell>
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                      {columns.map((column) => {
                        const value = row[column.id];
                        if (column.id === 'type') {
                          if (reagendamentos.isWhatsapp) {
                            return (
                              <M.TableCell key={column.id}>
                                <M.Grid container alignItems="center" spacing={4}>
                                  <M.Grid Item>
                                    <WhatsAppIcon fontSize="small" className={classes.icon} />
                                  </M.Grid>
                                  <M.Grid Item>
                                    {column.format && typeof value === 'number' ? column.format(value) : value}
                                  </M.Grid>
                                </M.Grid>
                              </M.TableCell>
                            );
                          }
                          return (
                            <M.TableCell key={column.id}>
                              <M.Grid container alignItems="center" spacing={4}>
                                <M.Grid Item>
                                  <MailOutlineIcon fontSize="small" className={classes.icon} />
                                </M.Grid>
                                <M.Grid Item>
                                  {column.format && typeof value === 'number' ? column.format(value) : value}
                                </M.Grid>
                              </M.Grid>
                            </M.TableCell>
                          );
                        }
                        return (
                          <M.TableCell key={column.id}>
                            {column.format && typeof value === 'number' ? column.format(value) : value}
                          </M.TableCell>
                        );
                      })}
                      <M.TableCell>
                        <UserData
                          phone={row.consultantPhone}
                          consultantName={row.consultantName}
                          requestNumber={row.rescheduleRequest}
                          clientName={row.clientName}
                          scheduledDate={row.scheduledDate}
                          rescheduleDate={row.reschedule}
                          consultantEmail={row.consultantEmail}
                        />
                        {/* <BtnReschedule
                          phone={row.consultantPhone}
                          consultantName={row.consultantName}
                          requestNumber={row.rescheduleRequest}
                          clientName={row.clientName}
                          scheduledDate={row.scheduledDate}
                          rescheduleDate={row.reschedule}
                        /> */}
                      </M.TableCell>
                    </M.TableRow>
                  );
                })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <CardVazio text="Não há solicitação de reagendamento!" icon={images.ReagendamentosVazio} />
      )}
    </>
  );
}
