export default function formatDate(date) {
  const dataFormatada = new Intl.DateTimeFormat('pt-BR').format(date);
  const day = dataFormatada.slice(0, 2);
  const month = dataFormatada.slice(3, 5);
  const year = dataFormatada.slice(6);

  return `${year}-${month}-${day}`;
}

export function formatDateForEnUS(date) {
  const day = date.slice(0, 2);
  const month = date.slice(3, 5);
  const year = date.slice(6);

  return new Date(`${month}-${day}-${year}`);
}

export function formatDateForSend(date) {
  const day = date.slice(0, 2);
  const month = date.slice(3, 5);
  const year = date.slice(6);

  return `${year}-${month}-${day}`;
}
