import * as M from '@material-ui/core';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import { useEvents } from '../../hooks/useEvents';
import AddEventos from '../AddEventos';
import EditEventos from '../EditEventos';
import RemoveEventos from '../RemoveEventos';
import { columns } from './Columns';
import { useStyles } from './styles';

export default function TableEventos({ eventos, ufOptions }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(eventos);
  const { downloadExcel } = useEvents();
  const classes = useStyles();

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: 'eventos cadastrados',
    downloadExcel,
  };

  return (
    <>
      {eventos && eventos.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <AddEventos ufOptions={ufOptions} />
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table">
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} align={column.align} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                      {columns.map((column) => {
                        const value = row[column.id];
                        return (
                          <M.TableCell key={column.id} align={column.align}>
                            {column.format && typeof value === 'number' ? column.format(value) : value}
                          </M.TableCell>
                        );
                      })}
                      <M.TableCell>
                        <EditEventos id={row.id} ufOptions={ufOptions} />
                      </M.TableCell>
                      <M.TableCell>
                        <RemoveEventos id={row.id} evento={row.evento} />
                      </M.TableCell>
                    </M.TableRow>
                  );
                })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <M.Grid className={classes.newEvent}>
            <AddEventos ufOptions={ufOptions} />
          </M.Grid>
          <CardVazio text="Nenhum evento cadastrado!" icon={images.EventosVazio} />
        </>
      )}
    </>
  );
}
