import * as M from '@material-ui/core';
import CompareArrowsOutlinedIcon from '@material-ui/icons/CompareArrowsOutlined';
import { connect } from 'react-redux';
import { useParams } from 'react-router-dom';
import PageTemplate from '../../components/PageTemplate';
import UserTable from './components/UserTable/index';
import { useUsers } from './hooks/useUsers';
import { useStyles } from './styles';

function RevisaoCadastro() {
  const { filteredLink } = useParams();
  const { users, isLoading } = useUsers(filteredLink);
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <CompareArrowsOutlinedIcon className={classes.img} />
          <M.Typography className={classes.titlePage}>Revisão de Cadastro</M.Typography>
          <M.Divider light className={classes.Divider} />
          <UserTable usuarios={users} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(RevisaoCadastro);
