import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import OutlinedFlagIcon from '@material-ui/icons/OutlinedFlag';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useAntecipacao } from '../../hooks/useAntecipacao';
import { useStyles } from './styles';

function ModalAnticipation({ id, name, payment }) {
  const { approvedAntecipacao } = useAntecipacao();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const handleAccept = () => {
    const log = { name, payment };
    const newStatus = {
      antecipation_request_id: id,
      granted: true,
      request_status: 'finalizada'
    };

    approvedAntecipacao(newStatus, handleClose, log);
  };

  return (
    <>
      <M.Button className={classes.btn} onClick={handleOpen}>
        <OutlinedFlagIcon />
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper elevation={3} className={classes.root}>
          <M.Grid
            container
            direction="column"
            justifyContent="space-evenly"
            alignItems="flex-start"
            className={classes.container}
          >
            <M.Grid item className={classes.textContainer}>
              <M.Typography className={classes.title}>Antecipação</M.Typography>
              <M.Divider className={classes.divider} />
              <M.Typography className={classes.text}>Você confirma a alteração de status?</M.Typography>
            </M.Grid>
            <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                <CloseIcon />
                NÃO
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={handleAccept}>
                <CheckIcon />
                SIM
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ModalAnticipation);
