/* eslint-disable camelcase */
import * as M from '@material-ui/core';
import LockOutlinedIcon from '@material-ui/icons/LockOutlined';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import AccessTable from './components/AccessTable';
import { TEXT_MODULE } from './constants/Text';
import { useAccess } from './hooks/useAccess';
import { useStyles } from './styles';

function Acessos() {
  const { access, isLoading } = useAccess();

  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid className={classes.containerTitle}>
            <LockOutlinedIcon className={classes.img} />
            <M.Typography className={classes.titlePage}>{TEXT_MODULE.TITLE}</M.Typography>
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <AccessTable access={access} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Acessos);
