import * as M from '@material-ui/core';
import images from '../../../../../assets/imgs';
import { CardVazio } from '../../../../../components/CardVazio';
import { TableHeader } from '../../../../../components/TableHeader';
import { useRequestSearch } from '../../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../../hooks/useSetPage';
import { usePestTable } from '../../../hooks/usePestTable';
import AddProduto from '../AddProduto';
import EditProduto from '../EditProduto';
import RemoveProduto from '../RemoveProduto';
import { columns } from './Columns';
import { useStyles } from './styles';

export default function TableProdutos({ produtos }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(produtos);
  const { downloadExcel } = usePestTable();
  const classes = useStyles();

  const plural = auxRows.length >= 2 ? 'itens cadastrados' : 'item cadastrado';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural,
    downloadExcel,
  };

  return (
    <>
      {produtos && produtos.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <AddProduto />
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table">
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} align={column.align} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                  return (
                    <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                      {columns.map((column) => {
                        const value = row[column.id];
                        return (
                          <M.TableCell key={column.id} align={column.align}>
                            {column.format && typeof value === 'number' ? column.format(value) : value}
                          </M.TableCell>
                        );
                      })}
                      <M.TableCell>
                        <EditProduto name={row.name} keyWord={row.keyWord} url={row.url} />
                      </M.TableCell>
                      <M.TableCell>
                        <RemoveProduto name={row.name} keyWord={row.keyWord} />
                      </M.TableCell>
                    </M.TableRow>
                  );
                })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <M.Grid className={classes.newProduto}>
            <AddProduto />
          </M.Grid>
          <CardVazio text="Não há nenhuma praga!" icon={images.PragasVazio} />
        </>
      )}
    </>
  );
}
