import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
  },
  container: {
    maxHeight: '50vh',
  },
  title: {
    backgroundColor: '#FFFFFF',
    fontWeight: 'bold',
  },
  text: {
    textTransform: 'capitalize',
  },
  icon: {
    margin: theme.spacing(0, 1, 0, 2),
  },
  img: {
    marginRight: theme.spacing(1),
  },
}));
