import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import OutlinedFlagOutlinedIcon from '@material-ui/icons/OutlinedFlagOutlined';
import RefreshOutlinedIcon from '@material-ui/icons/RefreshOutlined';
import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';
import { useState } from 'react';
import { connect } from 'react-redux';
import formatCPForCNPJ from '../../../../utils/helpers/formatCPForCNPJ';
import { useInclusao } from '../../hooks/useInclusao';
import { useStyles } from './styles';

const statsClient = [{ label: 'Cliente não confirmado' }, { label: 'Cliente confirmado' }];

function ChangesStats({ id, identification, name, phone, email }) {
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const [newPhone, setNewPhone] = useState(phone);
  const [newEmail, setNewEmail] = useState(email);
  const [idSap, setIdSap] = useState('');
  const [status, setStatus] = useState('');

  const classes = useStyles();

  const { checkIdSap, changeUser } = useInclusao();

  const closeModal = () => setModalIsOpen(false);

  const sendForm = () => {
    const data = {
      email,
      telephone: newPhone,
      sap_id: idSap,
      is_user_confirmed: status === 'Cliente confirmado'
    };

    changeUser(id, data);
  };

  return (
    <>
      <M.Button onClick={() => setModalIsOpen(true)}>
        <OutlinedFlagOutlinedIcon />
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid className={classes.head}>
            <M.Typography className={classes.titlePage}>Alterar status</M.Typography>
            <M.IconButton onClick={() => setModalIsOpen(false)}>
              <CloseIcon />
            </M.IconButton>
          </M.Grid>
          <M.Divider className={classes.divider} />
          <form onSubmit={sendForm} method="post" className={classes.container}>
            <M.Grid className={classes.blocoInput}>
              <M.FormControl disabled className={classes.input} variant="outlined">
                <M.InputLabel>Nome</M.InputLabel>
                <M.OutlinedInput value={name} labelWidth={45} />
              </M.FormControl>
              <M.FormControl disabled className={classes.input} variant="outlined">
                <M.InputLabel>CPNJ / CPF</M.InputLabel>
                <M.OutlinedInput value={identification} labelWidth={85} />
              </M.FormControl>
            </M.Grid>
            <M.Grid className={classes.blocoInput}>
              <M.FormControl required className={classes.input} variant="outlined">
                <M.InputLabel>Telefone Celular</M.InputLabel>
                <M.OutlinedInput
                  value={newPhone}
                  labelWidth={125}
                  onChange={(event) => setNewPhone(event.target.value)}
                />
              </M.FormControl>
              <M.FormControl required className={classes.input} variant="outlined">
                <M.InputLabel>Email</M.InputLabel>
                <M.OutlinedInput
                  value={newEmail}
                  labelWidth={50}
                  onChange={(event) => setNewEmail(event.target.value)}
                />
              </M.FormControl>
            </M.Grid>
            <M.Divider className={classes.divider} />
            <M.Grid className={classes.blocoSap}>
              <M.FormControl disabled className={classes.sap} variant="outlined">
                <M.InputLabel>ID SAP</M.InputLabel>
                <M.OutlinedInput value={!idSap ? 'N/A' : idSap} labelWidth={50} />
              </M.FormControl>
              {!idSap ? (
                <M.Button variant="outlined" onClick={() => checkIdSap(identification, setIdSap)}>
                  <RefreshOutlinedIcon style={{ marginRight: '5px' }} />
                  Procurar posição sap
                </M.Button>
              ) : (
                <M.FormControl className={classes.stats} variant="outlined">
                  <Autocomplete
                    className={classes.statsInput}
                    disablePortal
                    disableClearable
                    id="combo-box-demo"
                    options={statsClient}
                    sx={{ width: 300 }}
                    renderInput={(params) => <TextField {...params} label="Status" required />}
                    onChange={(event, value) => setStatus(value.label)}
                    value={status}
                  />
                </M.FormControl>
              )}
            </M.Grid>
            <M.Grid className={classes.btnForm}>
              <M.Button variant="outlined" onClick={closeModal}>
                <CloseIcon style={{ marginRight: '5px' }} />
                FEchar
              </M.Button>
              <M.Button disabled={!idSap} variant="outlined" type="submit" className={classes.btnSend}>
                <CheckIcon style={{ marginRight: '5px' }} />
                Alterar status
              </M.Button>
            </M.Grid>
          </form>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ChangesStats);
