import * as M from '@material-ui/core';
import Autocomplete from '@mui/material/Autocomplete';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import toast from 'react-hot-toast';
import { connect } from 'react-redux';
import ConfirmNotification from '../ConfirmNotification';
import { useStyles } from './styles';

const groupClientesMock = [
  { label: 'Usuários não clientes' },
  { label: 'Clientes não confirmados' },
  { label: 'Clientes confirmados' },
  { label: 'Todos' },
];

const filterGroupClient = {
  'Usuários não clientes': 'non_customer',
  'Clientes não confirmados': 'customer_not_confirmed',
  'Clientes confirmados': 'customer_confirmed',
};

function AddNotification() {
  const [title, setTitle] = useState('');
  const [description, setDescription] = useState('');
  const [groupClient, setGroupClient] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);

  const classes = useStyles();

  const closeModal = () => {
    setTitle('');
    setDescription('');
    setGroupClient('');
    setModalIsOpen(false);
    toast.remove();
  };

  const sendForm = (e) => {
    e.preventDefault();

    const data = {
      title,
      description,
      filter: filterGroupClient[groupClient],
    };

    toast(() => <ConfirmNotification data={data} closeModal={closeModal} group={groupClient} />, {
      style: {
        maxWidth: '463px',
      },
      duration: Infinity,
    });
  };

  return (
    <>
      <M.Button variant="outlined" className={classes.btnAddNotification} onClick={() => setModalIsOpen(true)}>
        <AddCircleOutlineIcon className={classes.imgAddCircle} />
        NOVA
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.titlePage}>Criar Notificação</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider className={classes.divider} />
            <form onSubmit={sendForm} method="post" className={classes.form}>
              <M.FormControl className={classes.title} variant="outlined" required>
                <M.InputLabel color="secondary">Título</M.InputLabel>
                <M.OutlinedInput
                  value={title}
                  color="secondary"
                  onChange={(event) => setTitle(event.target.value)}
                  labelWidth={50}
                />
              </M.FormControl>
              <M.FormControl className={classes.description} variant="outlined" required>
                <M.InputLabel color="secondary">Descrição</M.InputLabel>
                <M.OutlinedInput
                  required
                  value={description}
                  color="secondary"
                  onChange={(event) => setDescription(event.target.value)}
                  labelWidth={80}
                  multiline
                  rows={5}
                />
              </M.FormControl>
              <Autocomplete
                id="select-filter-group"
                options={groupClientesMock}
                sx={{ width: 250 }}
                renderInput={(params) => (
                  <M.TextField {...params} label="Grupo de Usuários" required variant="outlined" color="primary" />
                )}
                onInputChange={(event, newInputValue) => {
                  setGroupClient(newInputValue);
                }}
              />
              <M.Typography className={classes.textAlert}>
                Assim que for criada, a notificação será imediatamente enviada para o grupo selecionado.
              </M.Typography>
              <M.Grid className={classes.btnForm}>
                <M.Button variant="outlined" className={classes.btnCancel} onClick={closeModal}>
                  <CloseIcon />
                  CANCELAR
                </M.Button>
                <M.Button variant="outlined" className={classes.btnSend} type="submit">
                  <CheckIcon />
                  CRIAR NOTIFICAÇÃO
                </M.Button>
              </M.Grid>
            </form>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(AddNotification);
