import { FormControl, Grid, IconButton, InputAdornment, InputLabel, OutlinedInput } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import Typography from '@material-ui/core/Typography';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import api from '../../../../services/api';
import { logout } from '../../../../store/auth/actions';
import { openMessage } from '../../../../store/message/actions';
import images from '../../../../assets/imgs';
import { useStyles } from './styles';

function CardRedefinirSenha({ email, token, dispatch }) {
  const classes = useStyles();
  const history = useHistory();

  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);
  const [confirmPassword, setConfirmPassword] = useState('');
  const [showConfirmPassword, setShowConfirmPassword] = useState(false);

  const handleChange = () => {
    if (!password) {
      dispatch(openMessage('A senha não pode estar em branco.'));
    } else if (password.length < 8) {
      dispatch(openMessage('A senha deve conter pelo menos 8 caracteres.'));
    } else if (!confirmPassword) {
      dispatch(openMessage('A confirmação de senha não pode estar em branco.'));
    } else if (password !== confirmPassword) {
      dispatch(openMessage('A senhas digitadas não coincidem. Tente novamente.'));
    } else {
      api
        .post('/users/resetpassword', {
          email,
          token,
          password,
        })
        .then(
          (response) => {
            dispatch(openMessage('Senha redefinida com sucesso'));
            setPassword('');
            setConfirmPassword('');
            setTimeout(() => {
              history.push('/');
            }, 1000);
          },
          (error) => {
            switch (error.response.data.errors[0].message) {
              case 'Antecipation request id cannot be empty.':
                dispatch(openMessage('O limite do pedido de antecipação não pode estar vazio'));
                break;
              case 'Email cannot be empty.':
                dispatch(openMessage('E-mail não encontrado'));
                break;
              case 'Password must be at least 8 characters':
                dispatch(openMessage('A senha deve conter pelo menos 8 caracteres'));
                break;
              case 'Password cannot be empty.':
                dispatch(openMessage('A senha não pode estar em branco.'));
                break;
              case 'Token cannot be empty':
                dispatch(openMessage('Token não encontrado'));
                break;
              case 'Password does not conform to policy: Password must have lowercase characters.':
                dispatch(
                  openMessage(
                    'A senha não está em conformidade com a política: a senha deve conter caracteres minúsculos.',
                  ),
                );
                break;
              case 'Password does not conform to policy: Password must have symbol characters':
                dispatch(
                  openMessage(
                    '"A senha não está em conformidade com a política: a senha deve conter letras e simbolos Ex:(@, &, $)',
                  ),
                );
                break;
              case 'A internal error has ocurred.':
                dispatch(openMessage('Ocorreu um erro interno.'));
                break;
              case 'Invalid token':
                dispatch(openMessage('Pedido de alteração de senha inválido'));
                setTimeout(() => {
                  history.push('/recuperar-senha');
                }, 1000);
                break;
              case 'Expired token':
                dispatch(openMessage('Seu pedido de alteração de senha expirou'));
                setTimeout(() => {
                  history.push('/recuperar-senha');
                }, 1000);
                break;
              default:
                dispatch(openMessage('Ocorreu um erro ao alterar senha'));
            }
            if (error.response && error.response.status === 401) {
              dispatch(openMessage('Ocorreu um erro ao alterar senha'));
              setTimeout(() => {
                history.push('/recuperar-senha');
              }, 1000);
            }
          },
        );
    }
  };

  return (
    <Card className={classes.root} direction="row" alignItems="center">
      <CardContent>
        <Grid container direction="column" justifyContent="center" alignItems="center" spacing={2}>
          <img src={images.coloredLogo} alt="logo" alignItems="center" />
          <Typography className={classes.title}>CADASTRAR NOVA SENHA</Typography>
          <FormControl className={classes.input} variant="outlined">
            <InputLabel color="secondary">Nova senha</InputLabel>
            <OutlinedInput
              type={showPassword ? 'text' : 'password'}
              value={password}
              color="secondary"
              className={classes.password}
              onChange={(event) => setPassword(event.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowPassword(!showPassword)}
                    edge="end"
                    className={classes.eye}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
              labelWidth={83}
            />
          </FormControl>
          <FormControl className={classes.input} variant="outlined">
            <InputLabel color="secondary">Repita a nova senha</InputLabel>
            <OutlinedInput
              type={showConfirmPassword ? 'text' : 'password'}
              value={confirmPassword}
              color="secondary"
              onChange={(event) => setConfirmPassword(event.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password_2 visibility"
                    onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                    edge="end"
                    className={classes.eye}
                  >
                    {showConfirmPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
              labelWidth={142}
            />
          </FormControl>
        </Grid>
      </CardContent>
      <CardActions>
        <Grid container direction="column" justifyContent="center" alignItems="center">
          <Button variant="contained" size="small" color="primary" className={classes.btn} onClick={handleChange}>
            SALVAR ALTERAÇÃO
          </Button>
        </Grid>
      </CardActions>
    </Card>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(CardRedefinirSenha);
