import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import DeleteIcon from '@material-ui/icons/Delete';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useEvents } from '../../hooks/useEvents';
import { useStyles } from './styles';

function RemoveEventos({ id, evento }) {
  const { deleteEvents } = useEvents();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);
  const classes = useStyles();

  return (
    <>
      <M.IconButton onClick={handleOpen}>
        <DeleteIcon />
      </M.IconButton>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper elevation={3} className={classes.root}>
          <M.Grid
            container
            direction="column"
            justifyContent="space-evenly"
            alignItems="flex-start"
            className={classes.container}
          >
            <M.Grid item className={classes.textContainer}>
              <M.Typography className={classes.title}>Remover Evento</M.Typography>
              <M.Divider className={classes.divider} />
              <M.Typography className={classes.text}>
                Você confirma a remoção do evento <span style={{ fontWeight: 'bold' }}>{evento}</span>?
              </M.Typography>
            </M.Grid>
            <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                <CloseIcon />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={() => deleteEvents(id, evento, handleClose)}>
                <CheckIcon />
                CONFIRMAR
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(RemoveEventos);
