/* eslint-disable camelcase */
import * as M from '@material-ui/core';
import NotificationsNoneOutlinedIcon from '@material-ui/icons/NotificationsNoneOutlined';
import moment from 'moment';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import TooltipUi from '../../components/Tooltip';
import NotificationTable from './components/NotificationTable/index';
import { TEXT_MODULE } from './constants/Text';
import { useNotification } from './hooks/useNotification';
import { useStyles } from './styles';

const filterGroupClient = {
  non_customer: 'Usuários não clientes',
  customer_not_confirmed: 'Clientes não confirmados',
  customer_confirmed: 'Clientes confirmados',
}

function Notification() {
  const { notifications, isLoading } = useNotification();

  const classes = useStyles();

  const formatNotifications =
    notifications &&
    notifications.map(({ id, title, description, filter, created_at }) => {
      return {
        id,
        orderDate: moment(created_at).format('DD/MM/YYYY - HH:mm'),
        title,
        description: <TooltipUi text={description} limitCharacters={55} />,
        filter: filter === null ? 'Todos' : filterGroupClient[filter]
      };
    });

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid className={classes.containerTitle}>
            <NotificationsNoneOutlinedIcon className={classes.img} />
            <M.Typography className={classes.titlePage}>{TEXT_MODULE.TITLE}</M.Typography>
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <NotificationTable notifications={formatNotifications} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Notification);
