import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    padding: 24,
    minHeight: 100,
    display: 'flex',
    flexDirection: 'column',
  },
  head: {
    display: 'flex',
    justifyContent: 'space-between',
    alignItems: 'center',
  },
  divider: {
    width: 'inherit',
    margin: theme.spacing(1, 0, 1, 0),
  },
  titlePage: {
    fontWeight: 500,
    fontSize: 20,
  },
  container: {
    display: 'flex',
    flexDirection: 'column',
    gap: 15,
  },
  text: {
    fontWeight: 400,
    fontSize: 14,
  },
  textStrong: {
    fontWeight: 700,
    fontSize: 14,
  },
  btnForm: {
    display: 'flex',
    justifyContent: 'center',
    gap: 15,
    marginTop: 40,
  },
  btnCancel: {
    color: theme.appColors.pink,
    borderColor: theme.appColors.pink,
  },
  btnSend: {
    color: theme.appColors.green,
    borderColor: theme.appColors.green,
  },
}));
