import { Grid, Typography } from '@material-ui/core';
import DeleteOutlinedIcon from '@material-ui/icons/DeleteOutline';
import { useRef, useState } from 'react';
import { toast } from 'react-hot-toast';
import images from '../../assets/imgs';
import './styles.css';

export const DropImageInput = ({ image, onFileChange }) => {
  const wrapperRef = useRef(null);

  const [fileList, setFileList] = useState([]);

  const onDragEnter = () => wrapperRef.current.classList.add('dragover');
  const onDragLeave = () => wrapperRef.current.classList.remove('dragover');
  const onDrop = () => wrapperRef.current.classList.remove('dragover');

  const onFileDrop = (e) => {
    const newFile = e.target.files[0];
    if (newFile.size >= 1310720) {
      // eslint-disable-next-line no-alert
      toast.error('Tamanho da imagem ultrapassado.', {
        style: {
          background: '#E73434',
          color: '#FFFFFF',
        },
        iconTheme: {
          primary: '#FFFFFF',
          secondary: '#E73434',
        },
        duration: 10000,
      });
      setFileList([]);
      onFileChange([]);
    } else {
      const { name, size } = newFile;
      const data = { name, size, preview: URL.createObjectURL(newFile) };
      setFileList([data]);
      onFileChange(newFile);
    }
  };

  const fileRemove = () => {
    setFileList([]);
    onFileChange([]);
  };

  return (
    <>
      <Grid
        ref={wrapperRef}
        className="drop-file-input"
        onDragEnter={onDragEnter}
        onDragLeave={onDragLeave}
        onDrop={onDrop}
      >
        {fileList.length === 0 ? (
          <Grid className="drop-file-input__label">
            <img src={image || images.imagemDefault} alt="upload de imagem" />
            {!image && (
              <Typography>
                Arraste e solte uma imagem aqui ou <span className="sublimeText">escolha um arquivo</span>
              </Typography>
            )}
          </Grid>
        ) : (
          <Grid className="drop-file-input__label">
            <img src={fileList[0].preview} alt={fileList[0].name} />
            <Typography>{fileList[0].name}</Typography>
          </Grid>
        )}
        <input type="file" onChange={onFileDrop} />
      </Grid>
      {fileList.length === 0 ? (
        <Typography className="specText">
          A imagem deve ter no mínimo 366 x 305 pixels de dimensão, e no máximo 10 MB de tamanho.
        </Typography>
      ) : (
        // eslint-disable-next-line react/button-has-type
        <button className="btnRemove" onClick={fileRemove}>
          <DeleteOutlinedIcon />
          <Typography>REMOVER</Typography>
        </button>
      )}
    </>
  );
};
