import { Checkbox, FormControl, Grid, IconButton, InputAdornment, InputLabel, OutlinedInput } from '@material-ui/core';
import Button from '@material-ui/core/Button';
import Card from '@material-ui/core/Card';
import CardActions from '@material-ui/core/CardActions';
import CardContent from '@material-ui/core/CardContent';
import CircularProgress from '@material-ui/core/CircularProgress';
import Typography from '@material-ui/core/Typography';
import Visibility from '@material-ui/icons/Visibility';
import VisibilityOff from '@material-ui/icons/VisibilityOff';
import CryptoJS from 'crypto-js';
import React, { useEffect, useState } from 'react';
import { connect } from 'react-redux';
import { useHistory } from 'react-router-dom';
import { login, doesNotRememberAccess } from '../../../../store/auth/actions';
import { openMessage } from '../../../../store/message/actions';
import images from '../../../../assets/imgs';
import { useStyles } from './styles';

function CardLogin({ dispatch, auth }) {
  const classes = useStyles();
  const history = useHistory();

  const [isRememberAccess, setIsRememberAccess] = useState(false);
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');
  const [showPassword, setShowPassword] = useState(false);

  useEffect(() => {
    if (auth.token) {
      history.push('/dashboard');
    }
    if (auth.isRememberAccess) {
      setIsRememberAccess(true);
      setUsername(CryptoJS.AES.decrypt(auth.username.toString(), 'lavoroagro-portal').toString(CryptoJS.enc.Utf8));
      setPassword(CryptoJS.AES.decrypt(auth.password.toString(), 'lavoroagro-portal').toString(CryptoJS.enc.Utf8));
    }
  }, [auth]);

  const handleSignIn = () => {
    if (!username) {
      dispatch(openMessage('O login digitado não pode estar em branco. Tente novamente.'));
    } else if (!password) {
      dispatch(openMessage('A senha digitada não pode estar em branco. Tente novamente.'));
    } else {
      dispatch(login(username, password, isRememberAccess));
    }
  };

  const handleIsRememberAccess = (event) => {
    setIsRememberAccess(event.target.checked);
    if (!event.target.checked) {
      dispatch(doesNotRememberAccess());
    }
  };

  return (
    <Card className={classes.root} direction="row" alignItems="center">
      <CardContent>
        <Grid container direction="column" justifyContent="center" alignItems="center" spacing={2}>
          <img src={images.coloredLogo} alt="logo" alignItems="center" />
          <Typography className={classes.title}>BackOffice (Minha Lavoro App)</Typography>
          <FormControl className={classes.input} variant="outlined">
            <InputLabel color="secondary">Login</InputLabel>
            <OutlinedInput
              value={username}
              color="secondary"
              onChange={(event) => setUsername(event.target.value)}
              labelWidth={45}
            />
          </FormControl>
          <FormControl className={classes.input} variant="outlined">
            <InputLabel color="secondary">Senha</InputLabel>
            <OutlinedInput
              type={showPassword ? 'text' : 'password'}
              value={password}
              color="secondary"
              onChange={(event) => setPassword(event.target.value)}
              endAdornment={
                <InputAdornment position="end">
                  <IconButton
                    aria-label="toggle password visibility"
                    onClick={() => setShowPassword(!showPassword)}
                    edge="end"
                    className={classes.eye}
                  >
                    {showPassword ? <Visibility /> : <VisibilityOff />}
                  </IconButton>
                </InputAdornment>
              }
              labelWidth={50}
            />
          </FormControl>
          <Grid container direction="row" justifyContent="flex-start" alignItems="center" className={classes.checkbox}>
            <Checkbox name="checkedB" color="default" checked={isRememberAccess} onChange={handleIsRememberAccess} />
            <Typography className={classes.text}>Relembrar acesso</Typography>
          </Grid>
        </Grid>
      </CardContent>
      <CardActions>
        <Grid container direction="column" justifyContent="center" alignItems="center">
          {auth.isLoading ? (
            <CircularProgress />
          ) : (
            <Button variant="contained" size="small" color="primary" className={classes.btn} onClick={handleSignIn}>
              Entrar
            </Button>
          )}
          <Grid item container direction="row" justifyContent="flex-start" alignItems="center">
            <Typography className={classes.textBlue} onClick={() => history.push('/recuperar-senha')}>
              Esqueceu a senha ?{' '}
            </Typography>
          </Grid>
        </Grid>
      </CardActions>
    </Card>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(CardLogin);
