import Button from '@material-ui/core/Button';
import MailOutlineIcon from '@material-ui/icons/MailOutline';
import Avatar from '@mui/material/Avatar';
import Chip from '@mui/material/Chip';
import { useStyles } from './styles';

export default function Email({
  consultantEmail,
  consultantName,
  requestNumber,
  clientName,
  scheduledDate,
  rescheduleDate,
}) {
  const classes = useStyles();

  const handleOpenEmail = () => {
    window.open(
      `mailto:${consultantEmail}?subject= Solicitação de reagendamento ${requestNumber}&body= Olá ${consultantName} !%0AAqui é da Lavoro e gostaríamos de tirar uma dúvida sobre o pedido ${requestNumber} do cliente ${clientName} inicialmente agendado para ${scheduledDate} que foi solicitado reagendamento para ${rescheduleDate}.%0D%0A%0D%0AAtenciosamente,%0D%0A LAVORO`,
      '_blank',
    );
  };

  return (
    <Button className={classes.btn} onClick={handleOpenEmail}>
      <Chip
        className={classes.chip}
        avatar={
          <Avatar className={classes.avatar}>
            <MailOutlineIcon className={classes.icon} />
          </Avatar>
        }
        label={consultantEmail}
      />
    </Button>
  );
}
