import * as M from '@material-ui/core';
import PersonOutlineIcon from '@material-ui/icons/PersonOutline';
import ClearIcon from '@material-ui/icons/Clear';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import Email from '../Email';
import Name from '../Name';
import WHATS from '../WHATS';
import { useStyles } from './styles';

function userData({
  isWhatsapp,
  phone,
  consultantName,
  requestNumber,
  clientName,
  scheduledDate,
  rescheduleDate,
  consultantEmail,
}) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  return (
    <>
      <M.Button className={classes.btn} onClick={handleOpen}>
        <PersonOutlineIcon />
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper elevation={3} className={classes.root}>
          <M.Grid
            container
            direction="column"
            justifyContent="space-evenly"
            alignItems="flex-start"
            className={classes.container}
          >
            <M.Grid item className={classes.textContainer}>
              <M.Grid item container direction="row" justifyContent="space-between" alignItems="center">
                <M.Typography className={classes.title}>Dados do consultor</M.Typography>
                <M.IconButton className={classes.close} onClick={handleClose}>
                  <ClearIcon />
                </M.IconButton>
              </M.Grid>

              <M.Divider className={classes.divider} />
            </M.Grid>
            <M.Grid item className={classes.textContainer}>
              <M.Button disabled variant="contained" className={classes.btn2}>
                NOME
              </M.Button>
              <Name consultantName={consultantName} />
              <M.Divider className={classes.divider} />
            </M.Grid>
            {consultantEmail && (
              <M.Grid item className={classes.textContainer}>
                <M.Button disabled variant="contained" className={classes.btn2}>
                  E-MAIL
                </M.Button>
                <Email
                  consultantEmail={consultantEmail}
                  consultantName={consultantName}
                  requestNumber={requestNumber}
                  clientName={clientName}
                  scheduledDate={scheduledDate}
                  rescheduleDate={rescheduleDate}
                />
                <M.Divider className={classes.divider} />
              </M.Grid>
            )}
            {isWhatsapp && (
              <M.Grid item className={classes.textContainer}>
                <M.Button disabled variant="contained" className={classes.btn2}>
                  WHATS
                </M.Button>
                <WHATS
                  phone={phone}
                  consultantName={consultantName}
                  requestNumber={requestNumber}
                  clientName={clientName}
                  scheduledDate={scheduledDate}
                  rescheduleDate={rescheduleDate}
                />
                <M.Divider className={classes.divider} />
              </M.Grid>
            )}
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(userData);
