import { HashRouter as BrowserRouter, Route, Redirect } from 'react-router-dom';
import { connect } from 'react-redux';
import Login from '../pages/Login';
import RecuperarSenha from '../pages/RecuperarSenha';
import RedefinirSenha from '../pages/RedefinirSenha';
import Usuarios from '../pages/Usuários';
import RevisaoCadastro from '../pages/RevisaoCadastro';
import Limite from '../pages/Limite';
import Antecipacao from '../pages/Antecipacao';
import Eventos from '../pages/Eventos';
import Reagendamentos from '../pages/Reagendamentos';
import Campanhas from '../pages/Campanhas';
import Notification from '../pages/Notificacoes';
import MesaDeGraos from '../pages/MesaDeGraos';
import MesaDeGraosLocais from '../pages/MesaDeGraosLocais';
import Logs from '../pages/Logs';
import ProdutosSintomas from '../pages/ProdutosSintomas';
import Acessos from '../pages/Acessos';
import Dashboard from '../pages/Dashboard';
import Chamados from '../pages/Chamados';
import MesaDeGraosOrdensAutomaticas from '../pages/MesaDeGraosOrdensAutomaticas';
import InclusaoSap from '../pages/InclusaoSap';

const PrivateRoute = ({ component: Component, isAuthenticated, ...rest }) => (
  <Route
    {...rest}
    render={(props) =>
      isAuthenticated ? <Component {...props} /> : <Redirect to={{ pathname: '/', state: { from: props.location } }} />
    }
  />
);

function Routes({ auth }) {
  const { isAuthenticated } = auth;

  return (
    <BrowserRouter>
      <Route path="/" exact component={Login} />
      <Route path="/recuperar-senha" component={RecuperarSenha} />
      <Route path="/resetar-senha/:email/:token" component={RedefinirSenha} />
      <PrivateRoute path="/dashboard" component={Dashboard} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/acessos" component={Acessos} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/usuarios" exact component={Usuarios} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/usuarios/:filteredLink" component={Usuarios} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/inclusao-sap" component={InclusaoSap} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/revisao-de-cadastro" component={RevisaoCadastro} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/eventos" component={Eventos} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/revisao-de-limite" component={Limite} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/antecipacao" component={Antecipacao} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/reagendamento" component={Reagendamentos} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/campanhas" component={Campanhas} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/notificacoes" component={Notification} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/chamados" component={Chamados} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/mesa-de-graos" component={MesaDeGraos} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/mesa-de-graos-locais" component={MesaDeGraosLocais} isAuthenticated={isAuthenticated} />
      <PrivateRoute
        path="/mesa-de-graos-ordens-automaticas"
        component={MesaDeGraosOrdensAutomaticas}
        isAuthenticated={isAuthenticated}
      />
      <PrivateRoute path="/logs" component={Logs} isAuthenticated={isAuthenticated} />
      <PrivateRoute path="/lia" component={ProdutosSintomas} isAuthenticated={isAuthenticated} />
    </BrowserRouter>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Routes);
