import * as M from '@material-ui/core';
import { connect } from 'react-redux';
import { useCampanha } from '../../hooks/useCampanha';
import { useStyles } from './styles';

const SwitchDefault = M.styled(M.Switch)(({ theme }) => ({
  '& .MuiSwitch-switchBase.Mui-checked': {
    color: theme.appColors.switch,
    '&:hover': {
      backgroundColor: M.alpha(theme.appColors.switch, theme.palette.action.hoverOpacity),
    },
  },
  '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
    backgroundColor: theme.appColors.switch,
  },
}));

function ShowInCarousel({ id, checked}) {
  const { changeStatus } = useCampanha();
  const classes = useStyles();

  const labelDefault = checked ? 'Sim' : 'Não'

  return (
    <M.Grid className={classes.switch}>
      <SwitchDefault
        onChange={(e) => changeStatus(id, e.target.checked)}
        checked={checked}
        inputProps={{ 'aria-label': 'controlled' }}
      />
      <M.Typography className={classes.label}>{labelDefault}</M.Typography>
    </M.Grid>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ShowInCarousel);
