import * as M from '@material-ui/core';
import BuildOutlinedIcon from '@material-ui/icons/BuildOutlined';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import CloseIcon from '@material-ui/icons/Close';
import { useState } from 'react';
import { connect } from 'react-redux';
import { usePestTable } from '../../../hooks/usePestTable';
import { useStyles } from './styles';

function EditProduto({ name, keyWord, url }) {
  const { editPest } = usePestTable();
  const classes = useStyles();

  const [text, setText] = useState(url);
  const [open, setOpen] = useState(false);
  const handleClose = () => setOpen(false);
  const handleOpen = () => setOpen(true);

  const handleEdit = () => {
    const newPest = {
      name,
      keyword: keyWord,
      url: text,
    };

    editPest(newPest, handleClose);
  };

  return (
    <>
      <M.Button className={classes.btn} onClick={handleOpen}>
        <BuildOutlinedIcon className={classes.iconBtn} />
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid item className={classes.textContainer}>
            <M.Grid item container direction="row" justifyContent="space-between" alignItems="center">
              <M.Typography className={classes.title}>Editar url da praga/Doença</M.Typography>
              <M.IconButton className={classes.close} onClick={handleClose}>
                <ClearIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider className={classes.divider} />
          </M.Grid>

          <M.Grid
            container
            direction="row"
            justifyContent="space-around"
            alignItems="center"
            className={classes.container}
          >
            <M.FormControl className={classes.input} variant="outlined">
              <M.InputLabel color="secondary">Praga/Doença</M.InputLabel>
              <M.OutlinedInput value={name} defaultValue={name} disabled color="secondary" labelWidth={105} />
            </M.FormControl>
            <M.FormControl className={classes.input} variant="outlined">
              <M.InputLabel color="secondary">Palavras Chave</M.InputLabel>
              <M.OutlinedInput value={keyWord} defaultValue={keyWord} disabled color="secondary" labelWidth={110} />
            </M.FormControl>
            <M.FormControl className={classes.input2} variant="outlined">
              <M.InputLabel color="secondary">URL</M.InputLabel>
              <M.OutlinedInput
                value={text}
                color="secondary"
                onChange={(event) => setText(event.target.value)}
                labelWidth={30}
              />
            </M.FormControl>
            <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                <CloseIcon className={classes.iconBtn2} />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={handleEdit}>
                <CheckIcon className={classes.iconBtn2} />
                CONFIRMAR
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(EditProduto);
