import { useEffect, useState } from 'react';
import lodash from 'lodash';
import moment from 'moment';
import api from '../../../services/api';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';

export const useLogs = () => {
  const [logsList, setLogsList] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const showLogList = () => {
    api
      .get('/users/backoffice/logs')
      .then((response) => {
        setIsLoading(false);

        const data = response.data.data.map(({ email, changes, created_at: createdAt }) => ({
          created_at: moment(createdAt).format('DD/MM/YYYY - HH:mm'),
          email,
          changes,
        }));

        const orderData = lodash.orderBy(data, ['dateOrder'], ['desc']);
        setLogsList(orderData);
      })
      .catch((error) => {
        setIsLoading(false);
        if (error.response.data.errors[0].message === 'The incoming token has expired') {
          localStorage.clear();
          window.location.reload();
        }
      });
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Data_Hora: item.created_at,
      Email: item.email,
      Mudanças: item.changes,
    }));

    downloadXlsx(data, 'Alterações feitas.xlsx');
  };

  useEffect(() => {
    showLogList();
  }, []);

  return {
    logsList,
    isLoading,
    downloadExcel,
  };
};
