import * as M from '@material-ui/core';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import Autocomplete from '@mui/material/Autocomplete';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import AddCircleOutlineIcon from '@material-ui/icons/AddCircleOutline';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import React, { useState } from 'react';
import { connect } from 'react-redux';
import CheckIcon from '@material-ui/icons/Check';
import { useCampanha } from '../../hooks/useCampanha';
import { useStyles } from './styles';
import SwitchUi from '../../../../components/Switch';
import { DropImageInput } from '../../../../components/PickDropImage';
import formatDate from '../../../../utils/helpers/formatDate';

const publicoAlvoMock = [{ label: 'Clientes Lavoro' }, { label: 'Todos' }];
const positionMock = [{ label: '1' }, { label: '2' }, { label: '3' }, { label: '4' }, { label: '5' }];

function AddCampanha() {
  const { onSubmitCampanha } = useCampanha();

  const [image, setImage] = useState([]);
  const [title, setTitle] = useState('');
  const [initialDate, setInitialDate] = useState(null);
  const [endDate, setEndDate] = useState(null);
  const [description, setDescription] = useState('');
  const [url, setUrl] = useState('');
  const [client, setClient] = useState('');
  const [showInCarousel, setShowInCarousel] = useState(false);
  const [position, setPosition] = useState('');
  const [modalIsOpen, setModalIsOpen] = useState(false);
  const [errorIsOpen, setErrorIsOpen] = useState(false);
  const [msgError, setMsgError] = useState('');

  const styleProps = { display: errorIsOpen ? 'flex' : 'none' };

  const classes = useStyles(styleProps);

  const handleClose = () => {
    setTitle('');
    setInitialDate(null);
    setEndDate(null);
    setDescription('');
    setUrl('');
    setClient('');
    setPosition('');
    setImage([]);
    setModalIsOpen(false);
  };

  const sendForm = (e) => {
    e.preventDefault();
    const statusClient = client === 'Clientes Lavoro' ? true : false;

    const data = {
      title,
      start_date: formatDate(initialDate),
      end_date: formatDate(endDate),
      description,
      link_url: url,
      is_just_for_clients: statusClient,
      show_in_home: showInCarousel,
      position,
      image,
    };

    if (image.length === 0) {
      setMsgError('A imagem é de preenchimento obrigatório.');
      setErrorIsOpen(true);
    } else {
      onSubmitCampanha(data);
    }

    setTimeout(() => setErrorIsOpen(false), 15000);
  };

  return (
    <>
      <M.Button variant="outlined" className={classes.btnAddCampanha} onClick={() => setModalIsOpen(true)}>
        <AddCircleOutlineIcon className={classes.imgAddCircle} />
        NOVA
      </M.Button>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.head}>
              <M.Typography className={classes.titlePage}>Adicionar Campanha</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
            <M.Divider className={classes.divider} />
            <M.Grid className={classes.msgError}>
              <M.Typography>{msgError}</M.Typography>
            </M.Grid>
            <M.Grid className={classes.container}>
              <M.Grid>
                <DropImageInput onFileChange={setImage} />
              </M.Grid>
              <form encType="multipart/form-data" onSubmit={sendForm} method="post" className={classes.form}>
                <M.FormControl className={classes.title} variant="outlined" required>
                  <M.InputLabel color="secondary">Título da Campanha</M.InputLabel>
                  <M.OutlinedInput
                    value={title}
                    color="secondary"
                    onChange={(event) => setTitle(event.target.value)}
                    labelWidth={150}
                  />
                </M.FormControl>
                <M.Grid className={classes.dateContainer}>
                  <M.FormControl className={classes.dateForm} variant="standard">
                    <DatePicker
                      disablePast
                      inputFormat="dd/MM/yyyy"
                      value={initialDate}
                      onChange={(newValue) => {
                        setInitialDate(newValue);
                      }}
                      renderInput={(params) => (
                        <M.TextField {...params} label="Data de início" required variant="outlined" color="secondary" />
                      )}
                    />
                  </M.FormControl>
                  <M.FormControl className={classes.dateForm} variant="outlined">
                    <DatePicker
                      disablePast
                      inputFormat="dd/MM/yyyy"
                      value={endDate}
                      onChange={(newValue) => {
                        setEndDate(newValue);
                      }}
                      renderInput={(params) => (
                        <M.TextField
                          {...params}
                          label="Data de término"
                          required
                          variant="outlined"
                          color="secondary"
                        />
                      )}
                    />
                  </M.FormControl>
                </M.Grid>
                <M.FormControl className={classes.description} variant="outlined" required>
                  <M.InputLabel color="secondary">Descrição</M.InputLabel>
                  <M.OutlinedInput
                    required
                    value={description}
                    color="secondary"
                    onChange={(event) => setDescription(event.target.value)}
                    labelWidth={80}
                    multiline
                    rows={5}
                  />
                </M.FormControl>
                <M.FormControl className={classes.url} variant="outlined">
                  <M.InputLabel color="secondary">URL</M.InputLabel>
                  <M.OutlinedInput
                    className={classes.urlInput}
                    value={url}
                    color="secondary"
                    onChange={(event) => setUrl(event.target.value)}
                    labelWidth={30}
                  />
                </M.FormControl>
                <M.Grid className={classes.setContainer}>
                  <Autocomplete
                    disableClearable
                    id="select-client"
                    options={publicoAlvoMock}
                    sx={{ width: 170 }}
                    renderInput={(params) => (
                      <M.TextField {...params} label="Público-Alvo" required variant="outlined" color="secondary" />
                    )}
                    onInputChange={(event, newInputValue) => {
                      setClient(newInputValue);
                    }}
                  />
                  <SwitchUi label="Exibir no Carrossel" checked={showInCarousel} changeStatus={setShowInCarousel} />
                  <Autocomplete
                    disablePortal
                    disableClearable
                    id="select-position"
                    options={positionMock}
                    sx={{ width: 160 }}
                    renderInput={(params) => (
                      <M.TextField {...params} label="Posição" required variant="outlined" color="secondary" />
                    )}
                    onInputChange={(event, newInputValue) => {
                      setPosition(newInputValue);
                    }}
                  />
                </M.Grid>
                <M.Grid className={classes.btnForm}>
                  <M.Button variant="outlined" className={classes.btnCancel} onClick={handleClose}>
                    <CloseIcon />
                    CANCELAR
                  </M.Button>
                  <M.Button variant="outlined" className={classes.btnSend} type="submit">
                    <CheckIcon />
                    CONFIRMAR
                  </M.Button>
                </M.Grid>
              </form>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(AddCampanha);
