import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import CreateIcon from '@material-ui/icons/Create';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DatePicker from '@mui/lab/DatePicker';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import TimePicker from '@mui/lab/TimePicker';
import Autocomplete from '@mui/material/Autocomplete';
import TextField from '@mui/material/TextField';
import { connect } from 'react-redux';
import { useState, useEffect } from 'react';
import moment from 'moment';
import { useStyles } from './styles';
import { useEvents } from '../../hooks/useEvents';

function EditEventos({ id, ufOptions }) {
  const { changeEvents, handleEvents, handleStats, isLoading } = useEvents();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [name, setName] = useState('');
  const [date, setDate] = useState(new Date());
  const [hour, setHour] = useState(new Date());
  const [radioGroup, setRadioGroup] = useState('');
  const [uf, setUf] = useState('');
  const [city, setCity] = useState('');
  const [cityOptions, setCityOptions] = useState([]);
  const classes = useStyles();

  useEffect(() => {
    if (open) {
      const setEvents = {
        setName,
        setDate,
        setHour,
        setRadioGroup,
        setUf,
        setCity,
      };

      handleEvents(id, setEvents);
    }
  }, [open]);

  const handleUf = (event, value) => {
    if (value) {
      setUf(value.label);
      setCity('');
      handleStats(value, setCityOptions);
    }
  };

  const handleCity = (event, value) => {
    if (value) {
      setCity(value.label);
    }
  };

  const handleEdit = (e) => {
    e.preventDefault();
    if (radioGroup === 'br') {
      const dataNacional = {
        date: moment(date).format('YYYY-MM-DD'),
        hour: moment(hour).format('HH:mm'),
        name,
        uf: 'BR',
      };

      changeEvents(id, dataNacional, handleClose);
    } else {
      const data = {
        date: moment(date).format('YYYY-MM-DD'),
        hour: moment(hour).format('HH:mm'),
        name,
        uf,
        city,
      };

      changeEvents(id, data, handleClose);
    }
  };

  return (
    <>
      <M.IconButton onClick={handleOpen}>
        <CreateIcon />
      </M.IconButton>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid item className={classes.textContainer}>
              <M.Grid className={classes.head}>
                <M.Typography className={classes.titlePage}>Editar Evento</M.Typography>
                <M.IconButton onClick={handleClose}>
                  <CloseIcon />
                </M.IconButton>
              </M.Grid>
              <M.Divider className={classes.divider} />
            </M.Grid>
            <form onSubmit={handleEdit} method="post" className={classes.form}>
              <M.Grid container direction="row" alignItems="center" className={classes.container}>
                <M.FormControl className={classes.date} variant="outlined">
                  <DatePicker
                    inputFormat="dd/MM/yyyy"
                    label="Data"
                    value={date}
                    onChange={(newValue) => {
                      setDate(newValue);
                    }}
                    renderInput={(params) => <TextField {...params} required />}
                  />
                </M.FormControl>
                <M.FormControl className={classes.timer} variant="outlined">
                  <TimePicker
                    label="Horário"
                    ampm={false}
                    value={hour}
                    onChange={setHour}
                    renderInput={(params) => <TextField {...params} required />}
                  />
                </M.FormControl>
                <M.FormControl className={classes.input} required color="secondary" variant="outlined">
                  <M.InputLabel>Evento</M.InputLabel>
                  <M.OutlinedInput value={name} onChange={(event) => setName(event.target.value)} labelWidth={60} />
                </M.FormControl>
              </M.Grid>
              <M.Grid container direction="row" alignItems="center" className={classes.container}>
                <M.FormControl className={classes.radio} color="secondary" variant="outlined">
                  <M.FormLabel className={classes.radioLabel}>Localização</M.FormLabel>
                  <M.RadioGroup
                    row
                    aria-labelledby="demo-row-radio-buttons-group-label"
                    name="row-radio-buttons-group"
                    value={radioGroup}
                    onChange={(e) => setRadioGroup(e.target.value)}
                  >
                    <M.FormControlLabel value="br" control={<M.Radio />} label="Nacional" />
                    <M.FormControlLabel value="regional" control={<M.Radio />} label="Regional" />
                  </M.RadioGroup>
                </M.FormControl>
                <M.FormControl className={classes.uf} variant="outlined">
                  <Autocomplete
                    className={classes.uf}
                    disablePortal
                    disableClearable
                    disabled={radioGroup === 'br'}
                    id="combo-box-demo"
                    options={ufOptions}
                    sx={{ width: 300 }}
                    renderInput={(params) => <TextField {...params} label="UF" required />}
                    onChange={handleUf}
                    value={uf}
                  />
                </M.FormControl>
                <M.FormControl className={classes.cidade} variant="outlined">
                  <Autocomplete
                    disablePortal
                    disableClearable
                    disabled={radioGroup === 'br' || uf === ''}
                    id="combo-box-demo"
                    options={cityOptions}
                    renderInput={(params) => <TextField {...params} label="Cidade" required />}
                    onChange={handleCity}
                    value={city}
                  />
                </M.FormControl>
              </M.Grid>
              <M.Grid className={classes.btnForm}>
                <M.Button variant="outlined" className={classes.btnCancel} onClick={handleClose}>
                  <CloseIcon />
                  CANCELAR
                </M.Button>
                <M.Button variant="outlined" className={classes.btnSend} type="submit">
                  <CheckIcon />
                  CONFIRMAR
                </M.Button>
              </M.Grid>
            </form>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(EditEventos);
