import * as M from '@material-ui/core';
import { connect } from 'react-redux';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import { useCampanha } from '../../hooks/useCampanha';
import AddCampanha from '../AddCampanha';
import DeleteCampanha from '../DeleteCampanha';
import EditCampanha from '../EditCampanha';
import { columns } from './Columns';
import { useStyles } from './styles';

function CampanhasTable({ campanhas }) {
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();
  const { searchText, auxRows, requestSearch } = useRequestSearch(campanhas);
  const { downloadExcel } = useCampanha();
  const classes = useStyles();

  const plural = auxRows.length >= 2 ? 'campanhas cadastradas' : 'campanha cadastrada';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural,
    downloadExcel,
  };

  return (
    <>
      {campanhas && campanhas.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <AddCampanha />
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table" className={classes.table}>
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} style={column.style} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows &&
                  auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    return (
                      <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                        {columns.map((column) => {
                          const value = row[column.id];
                          return (
                            <M.TableCell key={column.id} align={column.align} style={column.style}>
                              {column.format && typeof value === 'number' ? column.format(value) : value}
                            </M.TableCell>
                          );
                        })}
                        <M.TableCell className={classes.btnCreate}>
                          <EditCampanha campanha={row} />
                        </M.TableCell>
                        <M.TableCell className={classes.btnDelete}>
                          <DeleteCampanha id={row.id} campanhaTitle={row.title} />
                        </M.TableCell>
                      </M.TableRow>
                    );
                  })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <M.Grid className={classes.newCampanha}>
            <AddCampanha />
          </M.Grid>
          <CardVazio text="Nenhuma campanha cadastrada!" icon={images.campanhaVazio} />
        </>
      )}
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(CampanhasTable);
