import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import ClearIcon from '@material-ui/icons/Clear';
import CloseIcon from '@material-ui/icons/Close';
import ContactMailIcon from '@material-ui/icons/ContactMail';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useAntecipacao } from '../../hooks/useAntecipacao';
import { useStyles } from './styles';

/**
 * @ConfigEmail Componente responsável pelo card de configuração de E-mail
 * @CriadoEm 19/09/2021
 */

function ConfigEmail({ email, emailCC, emailCCO }) {
  const { handleEdit } = useAntecipacao();
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const [de, setDe] = useState(email ? email.join(';') : '');
  const [cc, setCc] = useState(emailCC ? emailCC.join(';') : '');
  const [cco, setCco] = useState(emailCCO ? emailCCO.join(';') : '');

  const editEmails = { de, cc, cco, handleClose };

  return (
    <>
      <M.IconButton onClick={handleOpen}>
        <ContactMailIcon className={classes.img} />
      </M.IconButton>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>
          <M.Grid item className={classes.textContainer}>
            <M.Grid item container direction="row" justifyContent="space-between" alignItems="center">
              <M.Typography className={classes.title}>Configurações de E-mail</M.Typography>
              <M.IconButton className={classes.close} onClick={handleClose}>
                <ClearIcon />
              </M.IconButton>
            </M.Grid>

            <M.Divider className={classes.divider} />
          </M.Grid>
          <M.Grid item className={classes.textContainer}>
            <M.Typography className={classes.text}>
              Defina a conta remetente responsável pelo envio das solicitações de antecipação de pagamento. Se desejar
              receber uma cópia das solicitações, defina as contas nos respectivos campos (CC e CCO) separados por ponto
              e vírgula (;).
            </M.Typography>
          </M.Grid>
          <M.Grid
            container
            direction="row"
            justifyContent="space-around"
            alignItems="center"
            className={classes.container}
          >
            <M.FormControl className={classes.input} variant="outlined">
              <M.InputLabel color="secondary">De</M.InputLabel>
              <M.OutlinedInput
                value={de}
                color="secondary"
                onChange={(event) => setDe(event.target.value)}
                labelWidth={45}
              />
            </M.FormControl>
            <M.FormControl className={classes.input} variant="outlined">
              <M.InputLabel color="secondary">CC</M.InputLabel>
              <M.OutlinedInput
                value={cc}
                color="secondary"
                onChange={(event) => setCc(event.target.value)}
                labelWidth={45}
              />
            </M.FormControl>
            <M.FormControl className={classes.input} variant="outlined">
              <M.InputLabel color="secondary">CCO</M.InputLabel>
              <M.OutlinedInput
                value={cco}
                color="secondary"
                onChange={(event) => setCco(event.target.value)}
                labelWidth={45}
              />
            </M.FormControl>
            <M.Grid item container direction="row" justifyContent="center" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={handleClose}>
                <CloseIcon />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={() => handleEdit(editEmails)}>
                <CheckIcon />
                CONFIRMAR
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ConfigEmail);

/**
 * @prop email {array} array de  objetos contendo os pedidos de antecipação
 * @prop emailCC {array} array de  objetos contendo os pedidos de antecipação
 * @prop emailCCO {array} array de  objetos contendo os pedidos de antecipação
 * */
