import Button from '@material-ui/core/Button';
import WhatsAppIcon from '@material-ui/icons/WhatsApp';
import Avatar from '@mui/material/Avatar';
import Chip from '@mui/material/Chip';
import { useStyles } from './styles';

export default function WHATS({ phone, consultantName, requestNumber, clientName, scheduledDate, rescheduleDate }) {
  const classes = useStyles();
  const countryCode = '55';
  const number = String(phone).replace(/[^0-9,.]+/g, '');
  const phoneNumber = countryCode + number;

  const handleOpenWhatsapp = () => {
    window.open(
      `https://api.whatsapp.com/send?phone==${phoneNumber} &text=Olá ${consultantName} !%0AAqui é da Lavoro e gostaríamos de tirar uma dúvida sobre o pedido ${requestNumber} do cliente ${clientName} inicialmente agendado para ${scheduledDate} que foi solicitado reagendamento para ${rescheduleDate}.`,
      '_blank',
    );
  };

  return (
    <Button className={classes.btn} onClick={handleOpenWhatsapp}>
      <Chip
        className={classes.chip}
        avatar={
          <Avatar className={classes.avatar}>
            <WhatsAppIcon className={classes.icon} />
          </Avatar>
        }
        label={phone}
      />
    </Button>
  );
}
