/* eslint-disable import/order */
import { useState } from 'react';
import * as M from '@material-ui/core';
import images from '../../../../assets/imgs';
import { CardVazio } from '../../../../components/CardVazio';
import { TableHeader } from '../../../../components/TableHeader';
import { useRequestSearch } from '../../../../hooks/useRequestSearch';
import { useSetPage } from '../../../../hooks/useSetPage';
import ChangeStatusMesaDeGraos from '../ChangeStatusMesaDeGraos';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import { columns } from './Columns';
import { useStyles } from './styles';
import { useVendas } from '../../hooks/useVendas';

export default function NotificationTable() {
  const classes = useStyles();
  const { page, rowsPerPage, handleChangePage, handleChangeRowsPerPage } = useSetPage();

  const [status, setStatus] = useState('');
  const { notifications } = useVendas(status);

  const { searchText, auxRows, requestSearch } = useRequestSearch(notifications);

  const plural = auxRows.length >= 2 ? 'solicitações de vendas cadastradas' : 'solicitação de venda cadastrada';

  const tableHeaderProps = {
    auxRows,
    searchText,
    requestSearch,
    textTableHeader: plural,
  };

  return (
    <>
      {notifications && notifications.length > 0 ? (
        <M.Paper className={classes.root}>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <FormControl sx={{ m: 0.5, minWidth: 155, mr: 2 }} variant="standard" fullwidth size="small">
              <InputLabel id="demo-simple-select-label">Status</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={status}
                label="Todos"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="">Todos</MenuItem>
                <MenuItem value="Solicitado">Solicitado</MenuItem>
                <MenuItem value="Contrato Gerado">Contrato Gerado</MenuItem>
                <MenuItem value="Contrato Assinado">Contrato Assinado</MenuItem>
                <MenuItem value="Aprovado">Aprovado</MenuItem>
                <MenuItem value="Expirado">Expirado</MenuItem>
                <MenuItem value="Cancelado">Cancelado</MenuItem>
                <MenuItem value="Finalizado">Finalizado</MenuItem>
              </Select>
            </FormControl>
          </TableHeader>
          <M.TableContainer className={classes.container}>
            <M.Table stickyHeader aria-label="sticky table" className={classes.table}>
              <M.TableHead>
                <M.TableRow>
                  {columns.map((column) => (
                    <M.TableCell key={column.id} style={column.style} className={classes.title}>
                      {column.label}
                    </M.TableCell>
                  ))}
                  <M.TableCell className={classes.title} />
                </M.TableRow>
              </M.TableHead>
              <M.TableBody>
                {auxRows &&
                  auxRows.slice(page * rowsPerPage, page * rowsPerPage + rowsPerPage).map((row) => {
                    return (
                      <M.TableRow hover role="checkbox" tabIndex={-1} key={row.code}>
                        {columns.map((column) => {
                          const value = row[column.id];
                          return column.label === 'CPF/CNPJ' ? (
                            <M.TableCell
                              key={column.id}
                              align={column.align}
                              style={{ color: '#DB3F7A', textDecoration: 'underline', cursor: 'pointer' }}
                            >
                              {value}
                            </M.TableCell>
                          ) : (
                            <M.TableCell key={column.id} align={column.align} style={column.style}>
                              {column.format && typeof value === 'number' ? column.format(value) : value}
                            </M.TableCell>
                          );
                        })}
                        <M.TableCell className={classes.btnDelete}>
                          <ChangeStatusMesaDeGraos
                            id={row.id}
                            orderNumero={row.order_number}
                            statusAtual={row.status}
                          />
                        </M.TableCell>
                      </M.TableRow>
                    );
                  })}
              </M.TableBody>
            </M.Table>
          </M.TableContainer>
          <M.TablePagination
            rowsPerPageOptions={[10, 25, 50, 75, 100]}
            component="div"
            count={auxRows.length}
            rowsPerPage={rowsPerPage}
            page={page}
            onPageChange={handleChangePage}
            onRowsPerPageChange={handleChangeRowsPerPage}
          />
        </M.Paper>
      ) : (
        <>
          <TableHeader tableHeaderProps={tableHeaderProps}>
            <FormControl sx={{ m: 0.5, minWidth: 155, mr: 2 }} variant="standard" fullwidth size="small">
              <InputLabel id="demo-simple-select-label">Status</InputLabel>
              <Select
                labelId="demo-simple-select-label"
                id="demo-simple-select"
                value={status}
                label="Todos"
                onChange={(e) => setStatus(e.target.value)}
              >
                <MenuItem value="">Todos</MenuItem>
                <MenuItem value="Solicitado">Solicitado</MenuItem>
                <MenuItem value="Contrato Gerado">Contrato Gerado</MenuItem>
                <MenuItem value="Contrato Assinado">Contrato Assinado</MenuItem>
                <MenuItem value="Aprovado">Aprovado</MenuItem>
                <MenuItem value="Expirado">Expirado</MenuItem>
                <MenuItem value="Cancelado">Cancelado</MenuItem>
                <MenuItem value="Finalizado">Finalizado</MenuItem>
              </Select>
            </FormControl>
          </TableHeader>
          <CardVazio text="Nenhuma Solicitação de venda cadastrada!" icon={images.notificationVoid} />
        </>
      )}
    </>
  );
}
