import { useEffect, useState } from 'react';
import { toast } from 'react-hot-toast';
import lodash from 'lodash';
import moment from 'moment';
import api from '../../../services/api';
import { useLog } from '../../../hooks/useLog';
import downloadXlsx from '../../../utils/helpers/downloadXlsx';
import formatPhoneNumber from '../../../utils/helpers/formatPhoneNumber';

export const useUsers = (filteredLink) => {
  const [users, setUsers] = useState([]);
  const [isLoading, setIsLoading] = useState(true);

  const { sendLog } = useLog();

  const showUsers = () => {
    api.get('/registrations/requests').then(
      (response) => {
        setIsLoading(false);
        const data = response.data.data.map((user) => ({
          id: user.id,
          identification: user.user_identification,
          createdAt: moment(user.created_at).format('DD/MM/YYYY - HH:mm'),
          name: user.user_name,
          oldPhone: formatPhoneNumber(user.old_phone),
          newPhone: formatPhoneNumber(user.new_phone),
          oldEmail: user.old_email,
          newEmail: user.new_email,
        }));
        const orderData = lodash.orderBy(data, ['createdAt'], ['desc']);
        setUsers(orderData);
      },
      (error) => {
        setIsLoading(false);
        switch (error.response.data.errors[0].message) {
          case 'A internal error has ocurred.':
            toast.error('Ocorreu um erro interno.');
            break;
          case 'Unauthorized':
            toast.error('Token invalido, efetue o login novamente.');
            break;
          case 'The incoming token has expired':
            toast.error('Token invalido, efetue o login novamente.');
            localStorage.clear();
            window.location.reload();
            break;
          case 'Forbidden':
            toast.error('Sua sessão expirou, efetue o login novamente.');
            break;
          default:
            break;
        }
      },
    );
  };

  const changeInfoUser = (newRegitration, name) => {
    api
      .put('/registrations/requests', newRegitration)
      .then(() => {
        sendLog(`Cadastro do cliente “${name}” foi alterado.`);
        toast.success('O cadastro foi alterado com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch(() => {
        toast.error('Ocorreu um erro na revisão do cadastro. Tente novamente mais tarde. ');
      });
  };

  const deleteUserReview = (id, title) => {
    api
      .delete(`/registrations/requests/${id}`)
      .then(() => {
        sendLog(`Revisão do cliente “${title}” foi retirada.`);
        toast.success('A revisão foi retirada com sucesso!');
        setTimeout(() => {
          window.location.reload();
        }, 3000);
      })
      .catch((error) => console.log('=====error====', error));
  };

  const downloadExcel = (exportAuxRows) => {
    const data = exportAuxRows.map((item) => ({
      Cadastrado_Em: item.createdAt,
      Nome: item.name,
      Telefone_SAP: item.oldPhone,
      Telefone_alterado: item.newPhone,
      Email_SAP: item.oldEmail,
      Email_alterado: item.newEmail,
    }));
    downloadXlsx(data, 'Revisão de cadastro dos usuários.xlsx');
  };

  useEffect(() => {
    showUsers();
  }, [filteredLink]);

  return {
    users,
    isLoading,
    changeInfoUser,
    deleteUserReview,
    downloadExcel,
  };
};
