import { Grid } from '@material-ui/core';
import React from 'react';
import CardRecuperarSenha from './components/CardRecuperarSenha/index';
import { useStyles } from './styles';

function RecuperarSenha() {
  const classes = useStyles();

  return (
    <Grid container direction="row" justifyContent="center" alignItems="center" className={classes.bg}>
      <CardRecuperarSenha />
    </Grid>
  );
}

export default RecuperarSenha;
