import AUTH_ACTIONS_REQUEST from './types';

export const login = (username, password, isRememberAccess) => ({
  type: AUTH_ACTIONS_REQUEST.LOGIN,
  payload: {
    data: {
      username,
      password,
      isRememberAccess,
    },
  },
});

export const loginSuccess = (data) => ({
  type: AUTH_ACTIONS_REQUEST.LOGIN_SUCCESS,
  payload: {
    data,
  },
});

export const logout = () => ({
  type: AUTH_ACTIONS_REQUEST.LOGOUT,
});

export const request = () => ({
  type: AUTH_ACTIONS_REQUEST.REQUEST,
});

export const requestError = () => ({
  type: AUTH_ACTIONS_REQUEST.REQUEST_ERROR,
});

export const doesNotRememberAccess = () => ({
  type: AUTH_ACTIONS_REQUEST.DOES_NOT_REMEMBER_ACCESS,
});
