import * as M from '@material-ui/core';
import ClearIcon from '@material-ui/icons/Clear';
import SearchIcon from '@material-ui/icons/Search';
import images from '../../assets/imgs';
import { useStyles } from './styles';

export function TableHeader({ tableHeaderProps, children }) {
  const { auxRows, searchText, requestSearch, textTableHeader, downloadExcel } = tableHeaderProps;
  const classes = useStyles();

  return (
    <M.Grid container direction="row" justifyContent="space-between" alignItems="center">
      <M.Typography className={classes.textField}>
        {auxRows.length} {textTableHeader}
      </M.Typography>
      <M.Grid>
        <M.TextField
          variant="standard"
          color="secondary"
          value={searchText}
          onChange={(event) => requestSearch(event.target.value)}
          placeholder="Procurar por..."
          className={classes.textField}
          InputProps={{
            startAdornment: <SearchIcon fontSize="small" />,
            endAdornment: (
              <M.IconButton
                title="Clear"
                aria-label="Clear"
                size="small"
                style={{ visibility: searchText ? 'visible' : 'hidden' }}
                onClick={() => requestSearch('')}
              >
                <ClearIcon fontSize="small" />
              </M.IconButton>
            ),
          }}
        />
        {downloadExcel && (
          <M.Button variant="outlined" className={classes.btnExport} onClick={() => downloadExcel(auxRows)}>
            <img src={images.DownloadIcon} alt="logo" alignItems="center" className={classes.img} />
            BAIXAR TABELA
          </M.Button>
        )}
        {children}
      </M.Grid>
    </M.Grid>
  );
}
