import { alpha, Grid, styled, Switch, Typography } from '@material-ui/core';
import { useStyles } from './styles';

const SwitchDefault = styled(Switch)(({ theme }) => ({
  '& .MuiSwitch-switchBase.Mui-checked': {
    color: theme.appColors.switch,
    '&:hover': {
      backgroundColor: alpha(theme.appColors.switch, theme.palette.action.hoverOpacity),
    },
  },
  '& .MuiSwitch-switchBase.Mui-checked + .MuiSwitch-track': {
    backgroundColor: theme.appColors.switch,
  },
}));

export default function SwitchUi({ label, checked, changeStatus }, ...rest) {
  const classes = useStyles();

  return (
    <Grid className={classes.switch}>
      <SwitchDefault
        {...rest}
        onChange={(e) => changeStatus(e.target.checked)}
        checked={checked}
        inputProps={{ 'aria-label': 'controlled' }}
      />
      <Typography className={classes.label}>{label}</Typography>
    </Grid>
  );
}
