import * as M from '@material-ui/core';
import ContactSupportOutlinedIcon from '@material-ui/icons/ContactSupportOutlined';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import ChamadosTable from './components/ChamadosTable/index';
import { useChamados } from './hooks/useChamados';
import { useStyles } from './styles';

function Chamados() {
  const { chamados, isLoading } = useChamados();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <ContactSupportOutlinedIcon className={classes.img} />
          <M.Typography className={classes.titlePage}>Chamados</M.Typography>
          <M.Divider light className={classes.Divider} />
          <ChamadosTable chamados={chamados} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Chamados);
