import { makeStyles } from '@material-ui/core';

export const useStyles = makeStyles((theme) => ({
  root: {
    padding: 16,
    width: '100%',
    '&:hover': {
      boxShadow: `0px 2px 1px -1px ${theme.appColors.primaryColor}`,
    },
  },
  link: {
    textDecoration: 'none'
  },
  head: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'space-between',
  },
  title: {
    color: theme.appColors.blackColor,
    fontSize: 34,
  },
  icon: {
    fontSize: 29,
    color: theme.appColors.primaryColor,
  },
  text: {
    color: theme.appColors.blackColor,
    fontSize: 16,
    marginTop: 8,
    opacity: 0.6
  },
}));
