import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/Close';
import OutlinedFlagIcon from '@material-ui/icons/OutlinedFlag';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useLimits } from '../../hooks/useLimits';
import { useStyles } from './styles';

function ModalLimit({ id, name, value }) {
  const classes = useStyles();

  const [open, setOpen] = useState(false);
  const handleOpen = () => setOpen(true);
  const handleClose = () => setOpen(false);

  const { changeStatus } = useLimits();

  const log = { name, value };
  const refuse = { id, newStatus: 'no', handleClose, log };
  const accept = { id, newStatus: 'yes', handleClose, log };

  return (
    <>
      <M.Button className={classes.btn} onClick={handleOpen}>
        <OutlinedFlagIcon />
      </M.Button>
      <M.Modal
        open={open}
        onClose={handleClose}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper elevation={3} className={classes.root}>
          <M.Grid
            container
            direction="column"
            justifyContent="space-evenly"
            alignItems="flex-start"
            className={classes.container}
          >
            <M.Grid item className={classes.textContainer}>
              <M.Typography className={classes.title}>Alterar status</M.Typography>
              <M.Divider className={classes.divider} />
              <M.Typography className={classes.text}>Você confirma a alteração de status?</M.Typography>
            </M.Grid>
            <M.Grid item container direction="row" justifyContent="space-evenly" alignItems="flex-end">
              <M.Button variant="outlined" className={classes.btnNo} onClick={() => changeStatus(refuse)}>
                <CloseIcon />
                Reprovado
              </M.Button>
              <M.Button variant="outlined" className={classes.btnYes} onClick={() => changeStatus(accept)}>
                <CheckIcon />
                Aprovado
              </M.Button>
            </M.Grid>
          </M.Grid>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(ModalLimit);
