import produce from 'immer';
import MESSAGE_ACTION from './types';

const INITIAL_STATE = {
  isOpen: false,
  message: '',
};

export default function message(state = INITIAL_STATE, action) {
  switch (action.type) {
    case MESSAGE_ACTION.OPEN_MESSAGE:
      return produce(state, (draft) => {
        draft.isOpen = true;
        draft.message = action?.payload?.message;
      });

    case MESSAGE_ACTION.CLOSE_MESSAGE:
      return produce(state, (draft) => {
        draft.isOpen = false;
        draft.message = '';
      });

    default:
      return state;
  }
}
