import * as M from '@material-ui/core';
import HowToVoteIcon from '@material-ui/icons/HowToVote';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import ConfigEmail from './components/ConfigEmail';
import LimitTable from './components/LimitTable/index';
import { useLimits } from './hooks/useLimits';
import { useStyles } from './styles';

function Limite() {
  const { limites, isLoading } = useLimits();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid container direction="row" justifyContent="space-between" alignItems="center">
            <M.Grid item container direction="row" xs={7}>
              <HowToVoteIcon className={classes.img} />
              <M.Typography className={classes.titlePage}>Revisão de limites</M.Typography>
            </M.Grid>
            <ConfigEmail />
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <LimitTable limites={limites} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Limite);
