import { Tooltip, Typography } from '@material-ui/core';

export default function TooltipUi({ text, limitCharacters, type, data }) {
  const shortText = `${text?.slice(0, limitCharacters || 22)}...`;

  return !type ? (
    <Tooltip title={text} placement="top" arrow>
      <Typography>{shortText}</Typography>
    </Tooltip>
  ) : (
    <Tooltip
      title={
        <div>
          <p>{data.name}</p>
          <p>E-mail: {data.email}</p>
          <p>Tel: {data.sapTelephone}</p>
        </div>
      }
      placement="top"
      arrow
    >
      <Typography>{shortText}</Typography>
    </Tooltip>
  );
}
