import { useEffect, useState } from 'react';

export const useRequestSearch = (data) => {
  const [searchText, setSearchText] = useState('');
  const [auxRows, setAuxRows] = useState([]);

  function escapeRegExp(value) {
    return value.replace(/[-[\]{}()*+?.,\\^$|#\s]/g, '\\$&');
  }

  const requestSearch = (searchValue) => {
    setSearchText(searchValue);
    const searchRegex = new RegExp(escapeRegExp(searchValue), 'i');
    // eslint-disable-next-line no-shadow
    const filteredRows = data.filter((auxRows) => {
      return Object.keys(auxRows).some((field) => {
        return searchRegex.test(auxRows[field].toString());
      });
    });
    setAuxRows(filteredRows);
  };

  useEffect(() => {
    setAuxRows(data);
  }, [data]);

  return {
    searchText,
    auxRows,
    requestSearch,
  };
};
