import * as M from '@material-ui/core';
import CheckIcon from '@material-ui/icons/Check';
import CloseIcon from '@material-ui/icons/CloseOutlined';
import DeleteIcon from '@material-ui/icons/Delete';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import { useState } from 'react';
import { connect } from 'react-redux';
import { useLocais } from '../../hooks/useLocais';
import { useStyles } from './styles';


function DeleteLocalMesaDeGraos({ id, name }) {
  const { DeleteLocal } = useLocais();
  const classes = useStyles();

  const [modalIsOpen, setModalIsOpen] = useState(false);

  const confirmButton = (idLocal) => {
    setModalIsOpen(false);
    DeleteLocal(idLocal);
  };

  return (
    <>
      <M.IconButton onClick={() => setModalIsOpen(true)} className={classes.btnDelete}>
        <DeleteIcon />
      </M.IconButton>
      <M.Modal
        open={modalIsOpen}
        onClose={() => setModalIsOpen(false)}
        aria-labelledby="modal-modal-title"
        aria-describedby="modal-modal-description"
      >
        <M.Paper className={classes.root}>

        <M.Grid>
        <M.Grid className={classes.head}>
              <M.Typography className={classes.title}>Excluir Localidade</M.Typography>
              <M.IconButton onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
              </M.IconButton>
            </M.Grid>
        <M.Grid>
          <M.Divider className={classes.divider} />
        </M.Grid>
        <M.Typography className={classes.text}>
          Você confirma que realmente deseja excluir a localidade de  <strong style={{fontWeight: 'bold'}}>{name}?</strong>
        </M.Typography>
        <M.Typography>
          <p>
          Esta exclusão não inviabiliza os contratos de venda já efetuados.
          </p>
        </M.Typography>
        </M.Grid>
          <LocalizationProvider dateAdapter={AdapterDateFns}>
            <M.Grid className={classes.btnForm}>
              <M.Button variant="outlined" className={classes.btnCancel} onClick={() => setModalIsOpen(false)}>
                <CloseIcon />
                CANCELAR
              </M.Button>
              <M.Button variant="outlined" className={classes.btnSend} onClick={() => confirmButton(id)}>
                <CheckIcon />
                EXCLUIR LOCALIDADE
              </M.Button>
            </M.Grid>
          </LocalizationProvider>
        </M.Paper>
      </M.Modal>
    </>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(DeleteLocalMesaDeGraos);
