/* eslint-disable react/destructuring-assignment */
import * as M from '@material-ui/core';
import { Link } from 'react-router-dom';
import { useStyles } from './styles';

export default function DashboardCard({ quantities, text, icon, textWeight, filteredLink }) {
  const classes = useStyles();

  return (
    <M.Paper className={classes.root}>
      <Link to={`/usuarios${filteredLink}`} className={classes.link}>
        <M.Grid className={classes.head}>
          <M.Typography className={classes.title}>{quantities}</M.Typography>
          <M.Grid className={classes.icon}>{icon}</M.Grid>
        </M.Grid>
        <M.Typography className={classes.text}>
          {text} <span style={{ fontWeight: 700 }}>{textWeight}</span>
        </M.Typography>
      </Link>
    </M.Paper>
  );
}
