import { Grid, Typography } from '@material-ui/core';
import images from '../../assets/imgs';
import { useStyles } from './styles';

export default function Footer() {
  const classes = useStyles();

  return (
    <Grid container direction="row" justifyContent="flex-end" alignItems="center" className={classes.bg}>
      <Typography className={classes.versionText}>Backoffice Lavoro - Versão 1.10.2</Typography>
      <img src={images.iconFooter} alt="logo" alignItems="center" className={classes.img} />
    </Grid>
  );
}
