import * as M from '@material-ui/core';
import LocalAtmIcon from '@material-ui/icons/LocalAtm';
import { connect } from 'react-redux';
import PageTemplate from '../../components/PageTemplate';
import AnticipationTable from './components/AnticipationTable/index';
import ConfigEmail from './components/ConfigEmail';
import { useAntecipacao } from './hooks/useAntecipacao';
import { useStyles } from './styles';

/**
 * @Antecipacao Componente responsável pelo modulo de antecipação
 * @CriadoEm 19/09/2021
 */

function Antecipacao() {
  const { antecipacao, email, isLoading } = useAntecipacao();
  const classes = useStyles();

  return (
    <PageTemplate>
      {isLoading ? (
        <M.Grid item container direction="column" justifyContent="center" alignItems="center" xs={10}>
          <M.CircularProgress color="primary" />
        </M.Grid>
      ) : (
        <M.Grid item container direction="row" justifyContent="flex-start" alignItems="center" className={classes.page}>
          <M.Grid container direction="row" justifyContent="space-between" alignItems="center">
            <M.Grid item container direction="row" xs={10}>
              <LocalAtmIcon className={classes.img} />
              <M.Typography className={classes.titlePage}>Antecipação de Pagamento</M.Typography>
            </M.Grid>
            <ConfigEmail email={email.email} emailCC={email.emailCC} emailCCO={email.emailCCO} />
          </M.Grid>
          <M.Divider light className={classes.Divider} />
          <AnticipationTable antecipacao={antecipacao} />
        </M.Grid>
      )}
    </PageTemplate>
  );
}

export default connect((state) => ({
  auth: state.auth,
}))(Antecipacao);
